<template>
  <div class="equip_list mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="term.sku"
          placeholder="请输入酒店编码"
          @keyup.enter.native="getequiplist(1)"
          clearable
        ></el-input>
        <el-input
          v-model="term.hotelName"
          placeholder="请输入酒店名称"
          @keyup.enter.native="getequiplist(1)"
          clearable
        ></el-input>
        <el-input
          v-model="term.serialNo"
          placeholder="请输入设备编号"
          @keyup.enter.native="getequiplist(1)"
          clearable
        ></el-input>
        <el-select v-model="term.status" clearable filterable placeholder="请选择状态">
          <el-option v-for="item in statuslist" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>
        <el-select v-model="term.voltage" clearable filterable placeholder="请选择电压量">
          <el-option
            v-for="item in voltagelist"
            :key="item.id"
            :label="item.label"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-select v-model="term.netType" clearable filterable placeholder="请选择联网方式">
          <el-option v-for="item in netlist" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>
        <el-select v-model="term.powerType" clearable filterable placeholder="请选择供电方式">
          <el-option v-for="item in powerlist" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>
        <el-select v-model="term.areaId" clearable filterable placeholder="请选择区域" @focus="getarea">
