        <el-date-picker v-model="term.endTime" type="datetime" placeholder="请选择结束时间" align="right"></el-date-picker>
        <el-button type="primary" @click="getequiplist(1)">搜索</el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)" :disabled="userinfo.role==='common_role'">创建设备</el-button>
      </div>
    </div>

    <el-table
