      <el-table-column prop="hotelName" align="center" label="所在酒店"></el-table-column>
      <el-table-column prop="roomNo" align="center" label="所在位置" width="100"></el-table-column>
      <el-table-column align="center" label="联网方式" width="100">
        <template slot-scope="scope">{{scope.row.netType | fiternet}}</template>
      </el-table-column>
      <el-table-column align="center" label="供电方式" width="100">
