        </template>
      </el-table-column>
      <el-table-column align="center" label="休眠" width="130">
        <template slot-scope="scope">{{scope.row.sleepFlag==1?'是':''}}</template>
      </el-table-column>
      <el-table-column prop="updateTime" align="center" label="上次更新时间" width="170"></el-table-column>
      <el-table-column align="center" label="状态" width="100">
        <template slot-scope="scope">{{scope.row.status | fiterstatus}}</template>
      </el-table-column>
