<script>
import QRCode from "qrcodejs2";
import html2canvas from "html2canvas";
var cheackNumber = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入设备编号"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("设备编号不应该是小于0"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          callback(new Error("设备编号应该是一个正整数"));
        }
      }
    } else {
      callback(new Error("设备编号应该是一个正整数"));
    }
  }, 100);
};
let id = 0;
export default {
  name: "equip_list",
  data() {
    return {
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      Indnum: 0,
      loading: false,
      ispostion: false,
      DialogVisible: false,
      innerVisible: false,
      centerDialogVisible1: false,
      centerDialogVisible2: false,
      centerDialogVisible3: false,
      userinfo:{},
      arealist: [],
      equiplist: [],
      hotelList: [],
      options: [],
      goodslist: [],
      optionstype: [],
      device: [
        {
          id: 1,
          label: "1号机（10货道）"
        },
        {
          id: 3,
          label: "3号机（3货道）"
        }
      ],
      netlist: [
        {
          id: 1,
          label: "物联卡"
        },
        {
          id: 2,
          label: "wifi"
        }
      ],
      powerlist: [
        {
          id: 1,
          label: "电池"
        },
        {
          id: 2,
          label: "插电"
        }
      ],
      channstock: [
        {
          id: 1,
          label: "1"
        },
        {
          id: 0,
          label: "0"
        }
      ],
      statuslist: [
        {
          id: 1,
          label: "离线",
          disabled: true
        },
        {
          id: 2,
          label: "在线",
          disabled: true
        },
        {
          id: 4,
          label: "故障"
        }
      ],
      voltagelist: [
        {
          id: 1,
          label: ">80%"
        },
        {
          id: 2,
          label: "50%~80%"
        },
        {
          id: 3,
          label: "15%~50%"
        },
        {
          id: 4,
          label: "<15%"
        }
      ],
      actuserobj: {},
      term: {
        serialNo: "",
        name: "",
        status: "",
        voltage: "",
        sku: "",
        hotelName: "",
        netType: "",
        powerType: "",
        startTime: "",
        endTime: "",
        areaId: ""
      },
      channobj: {
        equipId: "",
        goodsId: "",
        id: "",
        serialNo: "",
        status: "",
        stock: "",
        roomNo: "",
        position: ""
      },
      Formedit: {
        serialNo: "",
        name: ""
      },
      ruleForm: {
        hotelId: "",
        id: "",
        name: "",
        remark: "",
        roomId: "",
        serialNo: "",
        status: "1",
        type: "",
        netType: "",
        powerType: "",
        channelList: []
      },
      rules: {
        name: [{ required: true, message: "请输入设备名称", trigger: "blur" }],
        serialNo: [
          { required: true, message: "请输入设备编号", trigger: "blur" }
        ],
        roomNo: [
          { required: true, message: "请输入所在位置", trigger: "blur" }
        ],
        roomId: [
          { required: true, message: "请选择所在位置", trigger: "change" }
        ],
        hotelId: [
          { required: true, message: "请选择酒店名称", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择设备状态", trigger: "change" }
        ],
        type: [
          { required: true, message: "请选择设备类型", trigger: "change" }
        ],
        netType: [
          { required: true, message: "请选择联网方式", trigger: "change" }
        ],
        powerType: [
          { required: true, message: "请选择供电方式", trigger: "change" }
        ],
        goodsId: [
          { required: true, message: "请选择货道商品", trigger: "change" }
        ],
        channstatus: [
          { required: true, message: "请选择货道状态", trigger: "change" }
        ]
      },
      newrules: {
        cheackgoods: [
          {
            required: true,
            message: "请选择货道商品",
            trigger: ["blur", "change"]
          }
        ],
        cheackstock: [
          {
            required: true,
            message: "请选择库存",
            trigger: ["blur", "change"]
          }
        ]
      },
      props: { label: "label", value: "value" }
    };
  },
  watch: {
    centerDialogVisible1: function() {
      if (!this.centerDialogVisible1) {
        for (let key in this.ruleForm) {
          if (this.ruleForm[key]) {
            this.ruleForm[key] = "";
          }
        }
        this.ruleForm.channelList = [];
        this.goodslist = [];
        this.actuserobj = {};
      }
    },
    centerDialogVisible2: function() {
      if (!this.centerDialogVisible2) {
        for (let key in this.ruleForm) {
          if (this.ruleForm[key]) {
            this.ruleForm[key] = "";
          }
        }
        this.ruleForm.channelList = [];
        this.goodslist = [];
        this.actuserobj = {};
      }
    },
    DialogVisible: function() {
      if (!this.DialogVisible) {
        for (let key in this.Formedit) {
          this.Formedit[key] = "";
        }
      }
    },
    innerVisible: function() {
      if (!this.DialogVisible) {
        for (let key in this.Formedit) {
          this.Formedit[key] = "";
        }
      }
    },
    "ruleForm.type": {
      handler(value) {
        if (value) {
          if (this.centerDialogVisible1) {
            this.setchannelList(value);
          }
        } else {
          this.ruleForm.channelList = [];
        }
      },
      deep: true
    },
    "ruleForm.hotelId": {
      handler(value) {
        if (value) {
          this.ispostion = true;
        } else {
          this.ispostion = false;
        }
      }
    }
  },
  filters: {
    fitertype: function(val) {
      if (val == 1) {
        return "6货道";
      } else if (val == 2) {
        return "8货道";
      } else if (val == 3) {
        return "10货道";
      }
    },
    fiternet: function(val) {
      if (val == 1) {
        return "物联卡";
      } else if (val == 2) {
        return "wifi";
      }
    },
    fiterpower: function(val) {
      if (val == 1) {
        return "电池";
      } else if (val == 2) {
        return "插电";
      }
    },
    fiterstatus: function(val) {
      if (val == 1) {
        return "离线";
      } else if (val == 2) {
        return "在线";
      } else if (val == 3) {
        return "停用";
      } else if (val == 4) {
        return "故障";
      } else if (val == 5) {
        return "作废";
      }
    },
    flterserialno: function(val) {
      if (val) {
        let _serialNo = val.substring(val.length - 8);
        return _serialNo;
      }
    },
    fitervoltage: function(val) {
      if (val == 1) {
        return ">80%";
      } else if (val == 2) {
        return "50%~80%";
      } else if (val == 3) {
        return "15%~50%";
      } else if (val == 4) {
        return "<15%";
      }
    }
  },
  methods: {
    //查询区域下拉列表
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("equip/areaList").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.options = _data;
        });
      }
    },
    //查询酒店下拉列表
    handleItemChange(val) {
      let _this = this;
      this.$http.get("equip/hotelList/" + val[0]).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (val[0] == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    handlechange(val) {
      if (this.centerDialogVisible1 || this.centerDialogVisible2) {
        this.ruleForm.hotelId = val[1];
      } else {
        this.term.hotelId = val[1];
      }
    },
    //查询商品分类列表
    getgoodstype(index) {
      const _this = this;
      this.Indnum = index;
      if (this.optionstype.length < 1) {
        this.$http.get("equip/type").then(res => {
          if (res.status == 200) {
            let _data = [];
            for (let i in res.data) {
              let _ob = {};
              _ob.value = res.data[i].id;
              _ob.label = res.data[i].name;
              // if(_this.centerDialogVisible2){
              //     _ob.disabled=true;
              // }
              _ob.children = [];
              _data.push(_ob);
            }

            this.optionstype = _data;
          }
        });
      }
    },
    //查询某类别下商品
    handleItemChangetype(val) {
      this.$http.get("equip/goods3?typeId=" + val[0]).then(res => {
        if (res.status == 200) {
          let _datas = [];
          for (let i in this.optionstype) {
            if (val[0] == this.optionstype[i].value) {
              for (let j in res.data) {
                let _obj = {};
                _obj.value = res.data[j].id;
                _obj.label = res.data[j].name;
                _obj.salePrice = res.data[j].salePrice;
                _datas.push(_obj);
              }
              this.optionstype[i].children = _datas;
            }
          }
        }
      });
    },
    handlechangetype(val) {
      this.ruleForm.channelList[this.Indnum].goodsId = val[1];
    },
    //查询设备商品
    copygoods(val) {
      if (val) {
        this.$http.get("equip/copyGoods?serialNo=" + val).then(res => {
          let _data = [...res.data];
          for (let i in _data) {
            for (let j in this.ruleForm.channelList) {
              if (
                Number(_data[i].serialNo) ==
                Number(this.ruleForm.channelList[j].serialNo)
              ) {
                this.ruleForm.channelList[j].goodsId = _data[i].goodsId;
                this.ruleForm.channelList[j].goodsName = _data[i].goodsName;
              }
            }
          }
          this.innerVisible = false;
        });
      } else {
        this.innerVisible = true;
      }
    },
    //查询商品列表
    getgoods(index) {
      this.Indnum = index;
      if (this.ruleForm.hotelId) {
        if (this.goodslist.length < 1) {
          let _url = "equip/goods?hotelId=" + this.ruleForm.hotelId;
          this.$http.get(_url).then(res => {
            this.goodslist = res.data;
          });
        }
      } else {
        this.$message.error("请先选择酒店");
      }
    },
    //查询列表数据
    getequiplist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url = "equip?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      if (this.term.startTime) {
        this.term.startTime = this.Until.changDage(this.term.startTime);
      }
      if (this.term.endTime) {
        this.term.endTime = this.Until.changDage(this.term.endTime);
      }

      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.$http.get(_Url).then(res => {
        if (res.status == 200) {
          let _data = [...res.data.data];
          _data.map(value => {
            if (value.updateTime) {
              value.updateTime = "20" + value.updateTime;
            }
          });
          this.equiplist = _data;
          this.total = res.data.total;
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getequiplist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getequiplist();
    },
    //打开编辑弹框
    openDialog(val) {
      let _this = this;
      this.disval = val;
      if (val == 1) {
        this.centerDialogVisible1 = !this.centerDialogVisible1;
      } else if (val == 2) {
        this.centerDialogVisible2 = !this.centerDialogVisible2;
      } else if (val == 3) {
        this.centerDialogVisible3 = !this.centerDialogVisible3;
      }
      if (val == 3) {
        setTimeout(() => {
          let qrcodeEL = document.getElementById("qrcode");
          let qrcode = new QRCode(qrcodeEL);
          document.getElementById("qrcode").innerHTML = "";
          _this.qrcode();
        }, 50);
      }
    },
    //点击编辑某条数据
    handleedit(obj) {
      this.getidinfo(obj.id);
      this.openDialog(2);
      this.actuserobj = obj;
    },
    //点击复制某条数据
    handlecopy(obj) {
      this.actuserobj = obj;
      this.DialogVisible = true;
    },
    //点击查看二维码
    handleqrcode(obj) {
      this.actuserobj = obj;
      this.getidinfo(obj.id);
      this.openDialog(3);
    },
    businessTypeChange(item) {
      this.ruleForm.channelList[this.Indnum].goodsId = item;
    },
    //通过ID查询某个数据信息
    getidinfo(id) {
      this.$http.get("equip/" + id).then(res => {
        let obj = { ...res.data };
        this.actuserobj.children = obj;
        obj.type = Number(obj.type);
        // obj.position = obj.roomNo?obj.roomNo:'';
        for (let i in obj.channelList) {
          obj.channelList[i].status =
            obj.channelList[i].status == 1 ? true : false;
          obj.channelList[i].serialNo = Number(obj.channelList[i].serialNo);
          obj.channelList[i].stock = Number(obj.channelList[i].stock);
        }
        obj.channelList.sort(function(a, b) {
          if (a.serialNo < b.serialNo) {
            return -1;
          } else if (a.serialNo > b.serialNo) {
            return 1;
          }
          return 0;
        });
        obj.remark = obj.remark ? obj.remark : "";
        this.ruleForm = JSON.parse(JSON.stringify(obj));
      });
    },
    //点击确认按钮，进行表单校验
    submitForm(formName) {
      if (this.ruleForm.roomNo) {
        this.ruleForm.position = this.ruleForm.roomNo;
      }
      if (this.centerDialogVisible1) {
        if (!this.ruleForm.roomNo) {
          this.$message.error("请输入所在位置");
          return;
        }
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (formName == "Formedit") {
            if (this.innerVisible) {
              this.copygoods(this.Formedit.serialNo);
            } else {
              this.savecopy();
            }
          } else if (formName == "ruleForm") {
            this.savedata();
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //重置
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
        this.ruleForm.remark = "";
      }
    },
    // 保存复制编辑
    savecopy() {
      let _Url = "equip/copy",
        _parms = {};
      _parms = {
        id: this.actuserobj.id,
        serialNo: this.Formedit.serialNo
      };
      if (this.Formedit.name) {
        _parms.name = this.Formedit.name;
      }
      this.$http.post(_Url, _parms).then(res => {
        this.$message.success("操作成功");
        this.getequiplist();
        this.DialogVisible = false;
      });
    },
    //保存修改编辑
    savedata() {
      let _Url = "equip",
        _channelList = [],
        _parms = {};
      console.log("ruleForm:", this.ruleForm);
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible1 = !this.centerDialogVisible1;
            this.getequiplist();
          }
        });
      } else if (this.disval == 2) {
        if (this.ruleForm.channelList.length > 0) {
          let _List = this.ruleForm.channelList;
          for (let i in _List) {
            let _obj = {
              id: _List[i].id,
              goodsId: _List[i].goodsId,
              stock: _List[i].stock
            };
            _channelList.push(_obj);
          }
        }
        _parms.channelList = _channelList;
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible2 = !this.centerDialogVisible2;
            this.getequiplist();
          }
        });
      }
    },
    //修改货道状态
    changestatus(obj) {
      let _Url = "equip/channel/status",
        _parms = {},
        _this = this;
      obj.status = obj.status ? 1 : 2;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          for (let i in _this.ruleForm.channelList) {
            if (obj.id == _this.ruleForm.channelList[i].id) {
              _this.ruleForm.channelList[i].status =
                obj.status == 1 ? true : false;
            }
          }
          this.$message.success("修改成功");
        }
      });
    },
    //保存货道信息修改
    handlesave(obj) {
      let _Url = "equip/channel",
        _parms = {};
      if (!obj.goodsId) {
        this.$message.error("请选择商品");
      } else if (!obj.status) {
        this.$message.error("请选择状态");
      } else {
        _parms = {
          goodsId: obj.goodsId,
          id: obj.id,
          status: obj.status,
          stock: obj.stock
        };
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
          }
        });
      }
    },
    //输入整数
    proving(obj) {
      obj.stock = obj.stock.replace(/[^\.\d]/g, "");
      obj.stock = obj.stock.replace(".", "");
    },
    //添加货道
    setchannelList(val) {
      let _length = val == 1 ? 10 : 3;
      this.ruleForm.channelList = [];
      for (let i = 0; i < _length; i++) {
        let _channobj = {
          goodsId: "",
          serialNo: Number(this.ruleForm.channelList.length) + 1,
          stock: val == 1 ? 1 : 7
        };
        this.ruleForm.channelList.push(_channobj);
      }
    },
    // 生成二维码
    qrcode() {
      let text = "";
      if (this.actuserobj.type == 1) {
        text = this.$GLOBAL.qrAPI + "index/" + this.actuserobj.serialNo; // 设置二维码内容或跳转地址
      } else if (this.actuserobj.type == 3) {
        // text =
        //   this.$GLOBAL.qrAPI +
        //   this.actuserobj.serialNo +
        //   "?type=" +
        //   this.actuserobj.type; // 设置二维码内容或跳转地址

        text =
          this.$GLOBAL.qrAPI + "index3.html?no=" + this.actuserobj.serialNo;
        // "&type=" +
        // this.actuserobj.type;; // 设置二维码内容或跳转地址
      }
      let qrcode = new QRCode("qrcode", {
        width: 114, // 设置宽度，单位像素
        height: 114, // 设置高度，单位像素
        text: text
      });
    },
    //下载设备二维码
    downloadImg() {
      html2canvas(document.querySelector("#imageWrapper")).then(canvas => {
        let dataURL = canvas.toDataURL("image/png");
        let a = document.createElement("a");
        let event = new MouseEvent("click");
        // 下载图名字
        a.download = this.actuserobj.hotelName + "——" + this.actuserobj.roomNo;
        //url
        a.href = dataURL;
        //合成函数，执行下载
        a.dispatchEvent(event);
      });
    },
    //打印二维码
    printqr() {
      let subOutputRankPrint = document.getElementById("imageWrapper");
      let newContent = subOutputRankPrint.innerHTML;
      let oldContent = document.body.innerHTML;
      document.body.innerHTML = newContent;
      document.title = "";
      // if (!!window.ActiveXObject || "ActiveXObject" in window) {
      //   this.remove_ie_header_and_footer();
      // }
      window.print();
      window.location.reload();
      document.body.innerHTML = oldContent;
      return false;
    }
  },
  created() {
    this.userinfo = this.$store.state.userInfo;
    this.getequiplist();
  }
};
</script>

<style lang="less">
.equip_list {
  .martop {
    .topleft {
      .el-input {
        width: 18%;
      }
      .el-select {
        width: 18%;
      }
    }
  }

  fieldset {
    padding: 5px;
    margin-bottom: 20px;
  }

  .qrimg {
    #qrcode {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    width: 140px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 20px;
    padding: 10px 5px;
    border: 1px solid pink;
  }
  .doloadbut {
    margin-left: 10%;
    float: left;
  }
  .el-dialog--center {
    min-width: 350px;
    height: 310px;
  }
  .demo-ruleForm {
    .el-select {
      width: 100%;
    }
    .el-cascader {
      width: 100%;
    }
  }
  .el-dialog__body {
    background: #fff;
  }
}
</style>




