
<template>
  <div class="goodsCheck mainboss">
    <div class="martop">
      <div class="topleft">
        <el-date-picker v-model="term.startDate" type="datetime" placeholder="开始时间" align="right"></el-date-picker>
        <el-date-picker v-model="term.endDate" type="datetime" placeholder="结束时间" align="right"></el-date-picker>
        <el-button type="primary" @click="getsales(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="saleslist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="operator" align="center" label="负责人"></el-table-column>
      <el-table-column prop="goodsName" align="center" label="商品"></el-table-column>
      <el-table-column prop="outNum" align="center" label="领货量"></el-table-column>
      <el-table-column prop="replenishNum" align="center" label="补货量"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "goodsCheck",
  data() {
    return {
      total: 0,
      ptotal: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      term: {
        startDate: "",
        endDate: ""
      },
      options: [],
      arealist: [],
      saleslist: []
    };
  },
  methods: {
    //获取列表数据
    getsales(val) {
      let _parms = {},
        _Url = "",
        _this = this;
      if (val == 1) {
        this.pageNum = 1;
      }
      if (this.term.startDate && this.term.endDate) {
        let _startDate = Number(this.term.startDate.getTime());
        let _endDate = Number(this.term.endDate.getTime());
        if (_startDate > _endDate) {
          this.term.endDate = "";
          this.$message.error("开始时间不得大于结束时间");
          return;
        } else if (_endDate - _startDate > 86400000 * 90) {
          this.term.endDate = "";
          this.$message.error("开始时间与结束时间间隔不得大于90天");
          return;
        }
      } else {
        this.$message("请选择时间");
        return;
      }

      if (this.term.startDate) {
        _parms.startTime = this.Until.changDage(this.term.startDate);
      }
      if (this.term.endDate) {
        _parms.endTime = this.Until.changDage(this.term.endDate);
      }
      _Url = "replenishVerify?";
      let _par = "";
      for (let key in _parms) {
        if (_parms[key]) {
          _Url += "&" + key + "=" + _parms[key];
        }
      }
      _par = _par.substr(1);
      _Url += _par;
      this.loading = true;
      this.$http.get(_Url).then(res => {
        console.log(res);
        this.loading = false;
        this.total = res.total;
        if (res.data.total < 1) {
          this.$message("没有查询到相关数据信息");
          this.saleslist = [];
        } else {
          setTimeout(() => {
            _this.saleslist = res.data;
          }, 200);
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getsales();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getsales();
    }
  },
  created() {
    const startDate = new Date();
    startDate.setTime(startDate.getTime() - 3600 * 1000 * 24 * 7 + 60);
    this.term.startDate = startDate;

    const endDate = new Date();
    endDate.setTime(endDate.getTime());
    this.term.endDate = endDate;
    this.getsales();
  }
};
</script>
<style lang="less">
.goodsCheck {
  .topleft {
    .el-input {
      width: 40%;
    }
  }
}
</style>



