<template>
  <div class="damage mainboss">
    <el-table
      :data="list"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      :row-class-name="tableRowClassName"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
     <!-- <template slot-scope="scope">{{scope.row.cityId==1?'武汉':'长沙'}}</template> -->
      <el-table-column prop="depotName" align="center" label="仓库"></el-table-column>
      <el-table-column prop="goodsName" align="center" label="商品"></el-table-column>
      <el-table-column prop="amount" align="center" label="数量"></el-table-column>
      <el-table-column prop="lossValue" align="center" label="损耗额"></el-table-column>
      <el-table-column align="center" label="时间">
          <template slot-scope="scope"> 20{{scope.row.time}}</template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
  </div>
</template>

<script>
export default {
  name: "damage",
  data() {
    return {
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      list: []
    };
  },
  methods: {
    //查询列表数据
    getlist() {
      let _Url = "loss?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      this.$http.get(_Url).then(res => {
        this.list = res.data.data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getlist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getlist();
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "gray";
      }
    }
  },
  created: function() {
    this.getlist();
  }
};
</script>


