

<template>
  <div class="category mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="name"
          placeholder="请输入商品名称"
          @keyup.enter.native="getgoodslist(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getgoodslist(1)">搜索</el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建类别</el-button>
      </div>
    </div>

    <el-table
      :data="goodslist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <!-- <el-table-column prop="firstName" align="center" label="一级类目"></el-table-column> -->
      <el-table-column prop="name" align="center" label="商品名称"></el-table-column>
      <el-table-column prop="level" align="center" label="级别"></el-table-column>
      <el-table-column align="center" label="状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="remark" align="center" label="备注"></el-table-column>
      <el-table-column label="操作" align="center" width="200">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="centerDialogVisible && disval==1"
      title="创建商品类别"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="名称" prop="name">
          <el-input v-model="ruleForm.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="级别" prop="level">
          <el-select v-model="ruleForm.level" clearable filterable placeholder="请选择级别">
            <el-option
              v-for="item in levellist"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="父级" v-show="ruleForm.level==2">
          <el-select v-model="ruleForm.parentId" clearable filterable placeholder="请选择父级">
            <el-option v-for="item in firstList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="备注">
          <el-input type="textarea" v-model="ruleForm.remark" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      v-if="centerDialogVisible && disval==2"
      title="编辑商品类别"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="名称" prop="name">
          <el-input v-model="ruleForm.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="级别" prop="level">
          <el-select v-model="ruleForm.level" clearable filterable placeholder="请选择级别">
            <el-option
              v-for="item in levellist"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="父级" v-show="ruleForm.level==2">
          <el-select v-model="ruleForm.parentId" clearable filterable placeholder="请选择父级">
            <el-option v-for="item in firstList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="备注">
          <el-input type="textarea" v-model="ruleForm.remark" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "category",
  data() {
    return {
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      isfirst: false,
      centerDialogVisible: false,
      name: "",
      goodslist: [],
      firstList: [],
      levellist: [
        // {
        //   id: 1,
        //   label: "一级"
        // },
        {
          id: 2,
          label: "二级"
        }
      ],
      ruleForm: {
        firstName: "",
        id: "",
        level: "",
        name: "",
        parentId: "",
        remark: "",
        status: ""
      },
      rules: {
        name: [{ required: true, message: "请输入商品名称", trigger: "blur" }],
        level: [{ required: true, message: "请选择级别", trigger: "change" }]
      }
    };
  },
  filters: {},
  watch: {
    centerDialogVisible: function() {
      if (!this.centerDialogVisible) {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
      }
    }
  },
  methods: {
    // //一级类别下拉菜单
    // getfirstList() {
    //   let _Url = "goodsType/firstList";
    //   if (this.firstList.length < 1 || this.isfirst) {
    //     this.$http.get(_Url).then(res => {
    //       if (res.status == 200) {
    //         this.firstList = res.data;
    //         this.isfirst = false;
    //       }
    //     });
    //   }
    // },
    //查询列表数据
    getgoodslist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "goodsType?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      if (this.name) {
        _Url += "&name=" + this.name;
      }
      this.$http.get(_Url).then(res => {
        let _data = res.data.data;
        for (let i in _data) {
          if (_data[i].status == 1) {
            _data[i].status = true;
          } else if (_data[i].status == 2) {
            _data[i].status = false;
          }
        }
        this.goodslist = _data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getgoodslist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getgoodslist();
    },
    //修改用户状态
    changestatus(obj) {
      let _Url = "goodsType/status",
        _parms = {};
      obj.status = obj.status ? 1 : 2;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getgoodslist();
        }
      });
    },
    //通过ID查询某个数据信息
    handleedit(obj) {
      this.$http.get("goodsType/" + obj.id).then(res => {
        if (res.status == 200) {
          for (let key in this.ruleForm) {
            this.ruleForm[key] = "";
          }
          for (let a in res.data) {
            for (let b in this.ruleForm) {
              if (a == b) {
                this.ruleForm[b] = res.data[a];
              }
            }
          }

          for (let i in this.firstList) {
            if (res.data.firstName == this.firstList[i].name) {
              this.ruleForm.parentId = this.firstList[i].id;
            }
          }

          this.openDialog(2);
        }
      });
    },
    //打开模态框
    openDialog(val) {
      // if (val == 1) {
      //   this.getfirstList();
      // }
      this.disval = val;
      this.centerDialogVisible = !this.centerDialogVisible;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //保存数据
    savedata() {
      let _Url = "goodsType",
        _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      this.isfirst = _parms.parentId ? true : false;
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getgoodslist();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getgoodslist();
          }
        });
      }
    }
  },
  created() {
    this.getgoodslist();
  }
};
</script>

<style lang="less">
.category {
  .topleft {
    .el-input {
      width: 70%;
    }
  }
  .el-pagination {
    float: left;
  }
  .thumbimg {
    width: 100px;
    height: 100px;
  }
  .tbimg {
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
  .diaimg {
    width: 400px;
    height: 400px;
    margin: 0 auto;
  }
  .el-dialog,
  .el-dialog--center {
    min-width: 450px !important;
  }
}
</style>


