

<template>
  <div class="depot mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-if="false"
          v-model="name"
          placeholder="请输入商品名称"
          @keyup.enter.native="getgoodslist(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getgoodslist(1)" v-if="false">搜索</el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建库存</el-button>
      </div>
    </div>

    <el-table
      :data="goodslist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column align="center" label="城市">
        <template slot-scope="scope">{{scope.row.cityId==1?'武汉':'长沙'}}</template>
      </el-table-column>
      <el-table-column prop="name" align="center" label="仓库名称"></el-table-column>
      <el-table-column prop="address" align="center" label="地址"></el-table-column>
      <el-table-column align="center" label="状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="remark" align="center" label="备注"></el-table-column>
      <el-table-column label="操作" align="center" width="200">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="centerDialogVisible  && disval==1"
      title="创建库存数据"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="仓库名称" prop="name">
          <el-input v-model="ruleForm.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="城市" prop="cityId">
          <el-select v-model="ruleForm.cityId" clearable filterable placeholder="请选择城市">
            <el-option v-for="item in citys" :key="item.value" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input v-model="ruleForm.address" clearable></el-input>
        </el-form-item>

        <!-- <el-form-item label="状态">
          <el-switch v-model="ruleForm.status" @change="changestatusf()" active-text inactive-text></el-switch>
        </el-form-item>-->

        <el-form-item label="备注">
          <el-input type="textarea" v-model="ruleForm.remark" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      v-if="centerDialogVisible && disval==2"
      title="编辑库存数据"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="仓库名称" prop="name">
          <el-input v-model="ruleForm.name" clearable></el-input>
        </el-form-item>
        <el-form-item label="城市" prop="cityId">
          <el-select v-model="ruleForm.cityId" clearable filterable placeholder="请选择城市">
            <el-option v-for="item in citys" :key="item.value" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input v-model="ruleForm.address" clearable></el-input>
        </el-form-item>

        <!-- <el-form-item label="状态">
          <el-switch v-model="ruleForm.status" @change="changestatusf()" active-text inactive-text></el-switch>
        </el-form-item>-->

        <el-form-item label="备注">
          <el-input type="textarea" v-model="ruleForm.remark" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "depot",
  data() {
    return {
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      centerDialogVisible: false,
      name: "",
      goodslist: [],
      citys: [
        {
          id: 1,
          label: "武汉"
        },
        {
          id: 2,
          label: "长沙"
        }
      ],
      ruleForm: {
        cityId: "",
        address: "",
        id: "",
        name: "",
        remark: "",
        status: ""
      },
      rules: {
        name: [{ required: true, message: "请输入商品名称", trigger: "blur" }],
        address: [{ required: true, message: "请输入位置", trigger: "blur" }],
        cityId: [{ required: true, message: "请选择城市", trigger: "change" }],
        status: [{ required: true, message: "请选择状态", trigger: "change" }]
      }
    };
  },
  filters: {},
  watch: {
    centerDialogVisible: function() {
      if (!this.centerDialogVisible) {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
      }
    }
  },
  methods: {
    //查询列表数据
    getgoodslist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url = "depot?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      if (this.name) {
        _Url += "&name=" + this.name;
      }
      this.$http.get(_Url).then(res => {
        let _data = res.data.data;
        for (let i in _data) {
          if (_data[i].status == 0) {
            _data[i].status = false;
          } else if (_data[i].status == 1) {
            _data[i].status = true;
          }
        }
        this.goodslist = _data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getgoodslist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getgoodslist();
    },
    changestatusf() {
      console.log("changestatusf", this.ruleForm.status);
      // this.ruleForm.status=!this.ruleForm.status;
    },
    //修改用户状态
    changestatus(obj) {
      console.log(obj);

      let _Url = "depot/status",
        _parms = {};
      obj.status = obj.status ? 1 : 0;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getgoodslist();
        }
      });
    },
    //通过ID查询某个数据信息
    handleedit(obj) {
      this.$http.get("depot/" + obj.id).then(res => {
        if (res.status == 200) {
          for (let key in this.ruleForm) {
            this.ruleForm[key] = "";
          }
          for (let a in res.data) {
            for (let b in this.ruleForm) {
              if (a == b) {
                this.ruleForm[b] = res.data[a];
              }
            }
          }
          console.log("ruleForm:", this.ruleForm);
          this.openDialog(2);
        }
      });
    },
    //打开模态框
    openDialog(val) {
      this.disval = val;
      this.centerDialogVisible = !this.centerDialogVisible;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //保存数据
    savedata() {
      let _Url = "depot",
        _parms = {};
      this.ruleForm.status = this.ruleForm.status ? 0 : 1;
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getgoodslist();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getgoodslist();
          }
        });
      }
    }
  },
  created() {
    this.getgoodslist();
  }
};
</script>

<style lang="less">
.depot {
  .topleft {
    .el-input {
      width: 70%;
    }
  }
  .el-pagination {
    float: left;
  }
  .thumbimg {
    width: 100px;
    height: 100px;
  }
  .tbimg {
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
  .diaimg {
    width: 400px;
    height: 400px;
    margin: 0 auto;
  }
  .el-dialog,
  .el-dialog--center {
    min-width: 450px !important;
  }
}
</style>


