<template>
  <div class="storeIn mainboss">
    <div class="martop">
      <div class="topleft"></div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建入库单</el-button>
      </div>
    </div>

    <el-table
      :data="storelist"
