        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="disval==1"
      title="创建入库单"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="50%"
      center
    >
      <el-form
        v-if="DialogVisible"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <fieldset>
          <legend>表单信息</legend>
          <el-form-item label="仓库" required>
            <el-col :span="6">
              <el-form-item prop="depotId">
                <el-select
                  v-model="ruleForm.depotId"
                  placeholder="请选择仓库"
                  clearable
                  @focus="getdepots"
                >
                  <el-option
                    v-for="item in depots"
                    :key="item.id"
