                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="入库类型" prop="type">
                <el-select v-model="ruleForm.type" placeholder="请选择入库类型" clearable>
                  <el-option
                    v-for="item in types"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="9">
              <el-form-item label="单号">
                <el-input v-model="ruleForm.serialNo" placeholder="请输入单号" maxlength="256" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="负责人" :required="ruleForm.type==2 || ruleForm.type==3?true:false">
            <el-col :span="6">
              <el-form-item>
                <el-select
                  v-model="ruleForm.operatorId"
                  placeholder="请选择负责人"
                  clearable
                  @focus="getoperatorlist"
                >
                  <el-option
                    v-for="item in operatorlist"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="入库时间" prop="time">
                <el-date-picker
                  v-model="ruleForm.time"
                  type="datetime"
                  placeholder="入库时间"
                  align="right"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="备注">
            <el-input
              v-model="ruleForm.remark"
              type="textarea"
              :rows="2"
              placeholder="请输入备注"
              maxlength="500"
              clearable
            ></el-input>
          </el-form-item>
        </fieldset>
        <fieldset>
          <legend>商品详情信息</legend>
          <el-button @click="addpurdetails" type="primary" icon="el-icon-plus" class="addbut">添加商品</el-button>
          <el-form-item
            label="选择商品"
            v-show="ruleForm.goodsList.length>0"
            v-for="(item,index) in ruleForm.goodsList"
            :key="index"
            :rules="rules.goodsId"
          >
            <el-col :span="9">
              <el-form-item>
                <el-cascader
                  :placeholder="item.goodsName?item.goodsName:'请选择商品'"
                  :show-all-levels="false"
                  :options="moregoods"
                  filterable
                  clearable
                  @focus="getgoodsType(index)"
                  @active-item-change="handleItemChange"
                  @change="handlechange"
                ></el-cascader>
              </el-form-item>
            </el-col>

            <el-col :span="10">
              <el-form-item
                label="数量"
                :rules="rules.amount"
                :prop="'goodsList.' + index +'.amount'"
              >
                <el-input
                  v-model="item.amount"
                  placeholder="请输入数量"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="1">
              <el-button @click="lesspurdetails(index)" type="primary" icon="el-icon-delete">删除</el-button>
            </el-col>
          </el-form-item>
        </fieldset>

        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      v-if="disval!=1"
      title="入库单详情"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="50%"
      center
    >
      <el-form
        v-if="DialogVisible"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <fieldset>
          <legend>表单信息</legend>
          <el-form-item label="仓库" required>
            <el-col :span="6">
              <el-form-item prop="depotId">
                <el-select
                  v-model="ruleForm.depotId"
                  placeholder="请选择仓库"
                  clearable
                  :disabled="true"
                  @focus="getdepots"
                >
                  <el-option
                    v-for="item in depots"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="入库类型" prop="type">
                <el-select v-model="ruleForm.type" placeholder="请选择入库类型" clearable :disabled="true">
                  <el-option
                    v-for="item in types"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="9">
              <el-form-item label="单号">
                <el-input v-model="ruleForm.serialNo" placeholder="请输入单号" maxlength="256" clearable :disabled="true"></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="负责人">
            <el-col :span="6">
              <el-form-item>
                <el-select
                  v-model="ruleForm.operatorId"
                  placeholder="请选择负责人"
                  clearable
                  :disabled="true"
                  @focus="getoperatorlist"
                >
                  <el-option
                    v-for="item in operatorlist"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="8">
              <el-form-item label="入库时间" prop="time">
                <el-date-picker
                  v-model="ruleForm.time"
                  type="datetime"
                  :disabled="true"
                  placeholder="入库时间"
                  align="right"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="备注">
            <el-input
              v-model="ruleForm.remark"
              type="textarea"
              :rows="2"
              :disabled="true"
              placeholder="请输入备注"
              maxlength="500"
              clearable
            ></el-input>
          </el-form-item>
        </fieldset>
        <fieldset>
          <legend>商品详情信息</legend>
          <!-- <el-button @click="addpurdetails" type="primary" icon="el-icon-plus" class="addbut"></el-button> -->
          <el-form-item
            label="选择商品"
            v-show="ruleForm.goodsList.length>0"
            v-for="(item,index) in ruleForm.goodsList"
            :key="index"
            :rules="rules.goodsId"
          >
            <el-col :span="9">
              <el-form-item>
                <el-cascader
                  :placeholder="item.goodsName?item.goodsName:'请选择商品'"
                  :show-all-levels="false"
                  :options="moregoods"
                  filterable
                  clearable
                  :disabled="true"
                  @focus="getgoodsType(index)"
                  @active-item-change="handleItemChange"
                  @change="handlechange"
                ></el-cascader>
              </el-form-item>
            </el-col>

            <el-col :span="10">
              <el-form-item label="数量" :rules="rules.amount">
                <el-input
                :disabled="true"
                  v-model="item.amount"
                  placeholder="请输入数量"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>

            <!-- <el-col :span="0.5">--</el-col>
            <el-col :span="1">
              <el-button @click="lesspurdetails(index)" type="primary" icon="el-icon-minus"></el-button>
            </el-col> -->
          </el-form-item>
        </fieldset>

        <!-- <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item> -->
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
var cheackNumber = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入金额"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("金额不应该小于0"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          value += " ";
          if (value.indexOf(".") != -1) {
            let arr = value.split(".");
            if (arr[1].length > 3) {
              callback(new Error("金额最多有两位小数"));
            } else {
              callback();
            }
          }
        }
      }
    } else {
      callback(new Error("金额应该是一个大于数的数字"));
    }
  }, 100);
};
var cheackNumber2 = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入一个数字"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      callback();
    } else {
      callback(new Error("请输入一个数字"));
    }
  }, 100);
};
export default {
  name: "storeIn",
  data() {
    return {
      inds: "",
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      DialogVisible: false,
      ruleForm: {
        depotId: "",
        goodsList: [],
        id: "",
        remark: "",
        serialNo: "",
        time: "",
        operatorId: "",
        status: "",
        type: "",
        userId: ""
      },
      rules: {
        depotId: [{ required: true, message: "请选择仓库", trigger: "change" }],
        type: [{ required: true, message: "请选择类型", trigger: "change" }],
        goodsId: [{ required: true, message: "请选择商品", trigger: "change" }],
        time: [
          { required: true, message: "请选择入库时间", trigger: "change" }
        ],
        amount: [{ required: true, validator: cheackNumber2, trigger: "blur" }],
        serialNo: [
          { required: true, message: "请输入设备号", trigger: "blur" }
        ],
        remark: [{ required: true, message: "请输入备注", trigger: "blur" }]
      },
      depots: [],
      types: [
        {
          id: 1,
          label: "采购入库"
        },
        {
          id: 2,
          label: "撤机入库"
        },
        {
          id: 3,
          label: "换货入库"
        },
        {
          id: 4,
          label: "其他"
        }
      ],
      goodsid: [],
      operatorlist: [],
      storelist: [],
      moregoods: []
    };
  },
  watch: {
    DialogVisible: function() {
      if (!this.DialogVisible) {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
        this.ruleForm.goodsList = [];
      } else {
        this.getdepots();
        this.getoperatorlist();
        // this.getgoodsType();
      }
    }
  },
  filters: {
    fitertype: function(val) {
      if (val == 1) {
        return "采购入库";
      } else if (val == 2) {
        return "撤机入库";
      } else if (val == 3) {
        return "换货入库";
      } else if (val == 4) {
        return "其他";
      }
    }
  },
  methods: {
    //查询仓库列表
    getdepots() {
      if (this.depots.length < 1) {
        this.$http.get("storeIn/depot").then(res => {
          if (res.status == 200) {
            for (let i in res.data) {
              res.data[i].id = Number(res.data[i].id);
            }
            this.depots = res.data;
          }
        });
      }
    },
    //查询负责人列表
    getoperatorlist() {
      if (this.operatorlist.length < 1) {
        this.$http.get("storeIn/operator").then(res => {
          if (res.status == 200) {
            for (let i in res.data) {
              res.data[i].id = Number(res.data[i].id);
            }
            this.operatorlist = res.data;
          }
        });
      }
    },
    //查询列表数据
    getlist() {
      let _Url =
        "storeIn?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      this.$http.get(_Url).then(res => {
        let _data = res.data.data;
        for (let i in _data) {
          _data[i].time = "20" + _data[i].time;
          if (_data[i].status == 0) {
            _data[i].status = false;
          } else if (_data[i].status == 1) {
            _data[i].status = true;
          }
        }
        this.storelist = _data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getlist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getlist();
    },
    //编辑
    handleedit(obj) {
      this.DialogVisible = true;
      this.disval = 2;
      let _Url = "storeIn/" + obj.id;
      this.$http.get(_Url).then(res => {
        if (res.status == 200) {
          res.data.time = new Date(res.data.time);
          for (let key in res.data) {
            if (res.data[key]) {
              this.ruleForm[key] = res.data[key];
            }
          }
        }
      });
    },
    //修改状态
    changestatus(obj) {
      let _Url = "storeIn/status",
        _parms = {};
      obj.status = obj.status ? 1 : 0;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getlist();
        }
      });
    },
    //打开弹框
    openDialog(val) {
      this.disval = val;
      if (this.ruleForm.goodsList && this.ruleForm.goodsList.length == 0) {
        let obj = {
          amount: "",
          goodsId: "",
          goodsName: "",
          storeId: ""
        };
        for(let i=0;i<3;i++){
         this.addpurdetails();
        }
      }
      this.DialogVisible = true;
    },
    //查询商品类别
    getgoodsType(index) {
      this.inds = index ? index : 0;
      let _Url = "storeIn/goodsType";
      if (this.moregoods.length < 1) {
        this.$http.get(_Url).then(res => {
          for (let i in res.data) {
            res.data[i].value = res.data[i].id;
            res.data[i].label = res.data[i].name;
            res.data[i].children = [];
          }
          this.moregoods = res.data;
        });
      }
    },
    handlechange(val) {
      let _this = this;
      let index = this.inds;
      setTimeout(() => {
        let _data = [];
        for (let i in this.moregoods) {
          if (val[0] == this.moregoods[i].id) {
            for (let j in this.moregoods[i].children) {
              if (val[1] == this.moregoods[i].children[j].value) {
                _data = this.moregoods[i].children[j];
              }
            }
          }
        }

        _this.ruleForm.goodsList[_this.inds].goodsId = _data.value;
        _this.ruleForm.goodsList[_this.inds].goodsName = _data.label;
      }, 500);
    },
    handleItemChange(val) {
      this.getgoods(val[0], 2);
    },
    //查询商品
    getgoods(val, ind, index) {
      let _Url = "storeIn/goods";
      this.inds = index;
      if (ind == 1) {
        _Url += "?sku=" + val;
      } else if (ind == 2) {
        _Url += "?typeId=" + val;
      }
      this.$http.get(_Url).then(res => {
        if (ind == 1) {
          let _data = [],
            isun = false;
          _data.push(res.data.id);
          res.data.label = res.data.name;
          res.data.value = res.data.id;

          for (let i in this.moregoods) {
            if (this.moregoods[i].id == res.data.id) {
              isun = false;
            } else {
              isun = true;
            }
          }
          if (isun) {
            this.moregoods.push(res.data);
          }

          this.ruleForm.goodsList[index].goodsId = res.data.id;
          this.ruleForm.goodsList[index].goodsid = _data;
        } else if (ind == 2) {
          let _datas = [];
          for (let i in this.moregoods) {
            if (val == this.moregoods[i].id) {
              for (let j in res.data) {
                let _obj = {};
                _obj.value = res.data[j].id;
                _obj.label = res.data[j].name;
                _datas.push(_obj);
              }
              _datas = this.unique(_datas);
              this.moregoods[i].children = _datas;
            }
          }
        }
      });
    },
    //数组去重
    unique(arr) {
      return Array.from(new Set(arr));
    },
    //添加商品
    addpurdetails() {
      let obj = {
        amount: "",
        goodsId: "",
        goodsName: "",
        storeId: ""
      };
      this.ruleForm.goodsList.push(obj);
    },
    //删除某件商品
    lesspurdetails(val) {
      let _pur = this.ruleForm.goodsList;
      for (let i in _pur) {
        if (val == i) {
          _pur.splice(i, 1);
        }
      }
    },
    //点击确认按钮，进行表单校验
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // console.log("success", this.ruleForm);
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //保存数据
    savedata() {
      let _Url = "storeIn",
        _parms = {},
        ruleObj = {},
        _Pur = [],
        _purdetails = this.ruleForm.goodsList;
      ruleObj = { ...this.ruleForm };
      if ( this.ruleForm.type == 2 ||  this.ruleForm.type == 4) {
        if (! this.ruleForm.operatorId) {
          this.$message("请选择负责人");
          return;
        }
      }
      if (ruleObj.time) {
        ruleObj.time = this.Until.changDage(ruleObj.time);
      }

      for (let key in ruleObj) {
        if (ruleObj[key]) {
          _parms[key] = ruleObj[key];
        }
      }

      for (let i in _purdetails) {
        if (_purdetails[i].amount) {
          let _obj = {};
          _obj.amount = _purdetails[i].amount;
          _obj.goodsId = _purdetails[i].goodsId;
          _obj.goodsName = _purdetails[i].goodsName;
          _obj.storeId = _purdetails[i].storeId;
          _Pur.push(_obj);
        }
      }
      _parms.goodsList = _Pur;
      if (_Pur.length == 0) {
        this.$message("请输入商品详情信息");
      } else {
        if (_parms.id) {
          this.$http.put(_Url, _parms).then(res => {
            if (res.status == 200) {
              this.$message.success("保存成功");
              this.DialogVisible = false;
              this.getlist();
            }
          });
        } else {
          this.$http.post(_Url, _parms).then(res => {
            if (res.status == 200) {
              this.$message.success("保存成功");
              this.DialogVisible = false;
              this.getlist();
            }
          });
        }
      }
    },
    //重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  created() {
    this.getlist();
  }
};
</script>

<style lang="less">
.storeIn {
  fieldset {
    padding: 5px;
    border: 1px solid pink;
    margin-bottom: 20px;
    position: relative;
    .addbut {
      position: relative;
      left:80%;
      margin-bottom: 10px;
    }
  }
}
</style>





