<template>
  <div class="access mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="term.goodsName"
          placeholder="请输入商品名称"
          @keyup.enter.native="getaccess(1)"
          clearable
        ></el-input>
        <el-select
          v-model="term.depotId"
          clearable
          filterable
          placeholder="请选择仓库"
          @focus="getdepot"
        >
          <el-option v-for="item in depot" :key="item.value" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <el-select v-model="term.type" clearable filterable placeholder="请选择类型">
          <el-option v-for="item in types" :key="item.id" :label="item.label" :value="item.id"></el-option>
        </el-select>
        <el-button type="primary" @click="getaccess(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="accesslist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="goodsName" align="center" label="商品名称"></el-table-column>
      <el-table-column align="center" label="类型">
        <template slot-scope="scope">{{scope.row.type==1?'入库':'出库'}}</template>
      </el-table-column>
      <el-table-column prop="depotName" align="center" label="仓库"></el-table-column>
      <el-table-column prop="amount" align="center" label="数量"></el-table-column>
      <el-table-column prop="time" align="center" label="时间"></el-table-column>
      <el-table-column prop="operationName" align="center" label="负责人"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "access",
  data() {
    return {
      total: 0,
      ptotal: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      term: {
        depotId: "",
        goodsName: "",
        type: ""
      },
      types: [
        {
          id: 1,
          label: "入库"
        },
        {
          id: 2,
          label: "出库"
        }
      ],
      depot: [],
      accesslist: []
    };
  },
  methods: {
    //查询类别数据
    getdepot() {
      if (this.depot.length < 1) {
        this.$http.get("storeDetail/depot").then(res => {
          if (res.status == 200) {
            this.depot = res.data;
          }
        });
      }
    },
    //获取列表数据
    getaccess(val) {
      let _parms = {},
        _Url = "storeDetail?",
        _this = this;
      if (val == 1) {
        this.pageNum = 1;
      }
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += key + "=" + this.term[key] + "&";
        }
      }
      _Url = _Url.substr(0, _Url.length - 1);
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        if (res.status == 200) {
          this.total = res.total;
          if (res.data.total < 1) {
            this.$message("没有查询到相关数据信息");
            this.accesslist = [];
          } else {
            for(let i in res.data.data){
              if(res.data.data[i].time){
                res.data.data[i].time='20'+res.data.data[i].time;
              }
            }
            _this.accesslist = res.data.data;
          }
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getaccess();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getaccess();
    }
  },
  created() {
    this.getaccess();
  }
};
</script>
<style lang="less">
.access {
  .topleft {
    .el-input {
      width: 25%;
    }
  }
}
</style>



