
<template>
  <div class="freeroom mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="term.hotelName"
          placeholder="请输入酒店名称"
          @keyup.enter.native="getloglist(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getloglist(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="loglist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="hotelName" align="center" label="酒店名称"></el-table-column>
      <el-table-column prop="roomNo" align="center" label="房间号"></el-table-column>
      <el-table-column prop="equipNo" align="center" label="设备号"></el-table-column>
      <el-table-column prop="createTime" align="center" label="抽奖时间"></el-table-column>
      <el-table-column align="center" label="已兑奖">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            :disabled="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="phone" align="center" label="联系电话"></el-table-column> -->
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
  </div>
</template>

<script>
export default {
  name: "freeroom",
  data() {
    return {
      pageNum: 1,
      pageSize: 10,
      total: 0,
      loading: false,
      loglist: [],
      options: [],
      term: {
        hotelName: ""
      }
    };
  },

  methods: {
    //查询补货日志数据
    getloglist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "/freeRoom?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        let _data=[...res.data.data];
        for (let i in _data) {
          if (_data[i].status == 1) {
            _data[i].status = true;
          } else if (_data[i].status == 0) {
            _data[i].status = false;
          }
          _data[i].createTime='20'+_data[i].createTime;
        }
        
        this.loglist = _data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getloglist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getloglist();
    },
    changestatus(obj) {
      let _Url = "freeRoom",
        _parms = {};
      // newObj = JSON.parse(JSON.stringify(obj));
      obj.status = obj.status ? 1 : 0;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        console.log('res:', res)
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getloglist();
        }
      });
    }
  },
  created() {
    this.getloglist();
  }
};
</script>

<style lang="less">
.freeroom {
  .topleft {
    .el-input {
      width: 60%;
    }
  }
}
</style>







