
<template>
  <div class="area_list mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="areaName"
          placeholder="请输入区域名称"
          @keyup.enter.native="getarea(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getarea(1)">搜索</el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建区域</el-button>
      </div>
    </div>
    <el-table
      :data="arealist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
      style="width: 100%"
    >
      <el-table-column align="center" label="城市">
        <template slot-scope="scope">{{scope.row.cityId | filtercityId}}</template>
      </el-table-column>
      <el-table-column prop="name" align="center" label="区域名称"></el-table-column>
      <!-- <el-table-column prop="seq" align="center" label="排序"></el-table-column> -->
      <!-- <el-table-column prop="hotelNo" align="center" label="酒店数量"></el-table-column>
      <el-table-column prop="roomNo" align="center" label="房间数量"></el-table-column>-->
      <!-- <el-table-column align="center" label="状态">
        <template slot-scope="scope">{{scope.row.status | filterstatus}}</template>
      </el-table-column>-->
      <el-table-column align="center" label="当前状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" align="center" width="200">
        <template slot-scope="scope">
          <el-button
            :disabled="!scope.row.status"
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
          <el-button
            :disabled="!scope.row.status"
            @click="distribution(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >分配酒店</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="centerDialogVisible1"
      title="创建区域"
      :visible.sync="centerDialogVisible1"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="城市" prop="cityId">
          <el-select v-model="ruleForm.cityId" clearable filterable placeholder="请选择城市">
            <el-option v-for="item in citys" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="区域名称" prop="name">
          <el-input v-model="ruleForm.name" placeholder="请输入区域名称" maxlength="20" clearable></el-input>
        </el-form-item>

        <el-form-item label="排序" prop="seq">
          <el-input
            v-model="ruleForm.seq"
            placeholder="请输入排序序号"
            type="number"
            oninput="if(value.length>11)value=value.slice(0,11)"
            clearable
          ></el-input>
        </el-form-item>

        <el-form-item label="备注" prop="remark">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入备注"
            v-model="ruleForm.remark"
            maxlength="100"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      v-if="centerDialogVisible2"
      title="编辑区域"
      :visible.sync="centerDialogVisible2"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="城市" prop="cityId">
          <el-select v-model="ruleForm.cityId" clearable filterable placeholder="请选择城市">
            <el-option v-for="item in citys" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="区域名称" prop="name">
          <el-input v-model="ruleForm.name" placeholder="请输入区域名称" maxlength="20" clearable></el-input>
        </el-form-item>

        <el-form-item label="排序" prop="seq">
          <el-input
            v-model="ruleForm.seq"
            placeholder="请输入排序序号"
            type="number"
            oninput="if(value.length>11)value=value.slice(0,11)"
            clearable
          ></el-input>
        </el-form-item>

        <el-form-item label="备注" prop="remark">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入备注"
            v-model="ruleForm.remark"
            maxlength="100"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="分配酒店"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      width="80%"
      center
    >
      <el-checkbox-group v-model="checkList">
        <el-checkbox v-for="item in hotellist" :key="item.id" :label="item.id">{{item.name}}</el-checkbox>
      </el-checkbox-group>
      <span class="pagein dialogpag" v-if="DialogVisible">
        <el-pagination
          layout="total, sizes, prev, pager, next, jumper"
          :page-sizes="[40]"
          :page-size="40"
          :total="hoteltoal"
          @size-change="hotelhandleSizeChange"
          @current-change="hotelhandleCurrentChange"
        ></el-pagination>
      </span>
      <el-button type="primary" @click="confirmEdit" style="margin-left:40%;margin-top:10px;">确认修改</el-button>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "perlist",
  data() {
    return {
      total: 0,
      hoteltoal: 0,
      disval: 1,
      pageNum: 1,
      pageSize: 10,
      hotel_pageNum: 1,
      hotel_pageSize: 40,
      areaName: "",
      loading: false,
      DialogVisible: false,
      centerDialogVisible1: false,
      centerDialogVisible2: false,
      checkList: [],
      arealist: [],
      hotellist: [],
      oldhotellist: [],
      actuserobj: {},
      ruleobj: {},
      statuslist: [
        {
          id: 1,
          label: "有效"
        },
        {
          id: 2,
          label: "无效"
        }
      ],
      citys: [
        {
          id: 1,
          label: "武汉"
        },
        {
          id: 2,
          label: "长沙"
        }
      ],
      // parentmenulist: [],
      ruleobj: {},
      ruleForm: {
        name: "",
        parentId: "",
        remark: "",
        seq: "",
        status: "",
        cityId: ""
      },
      rules: {
        cityId: [
          { required: true, message: "请选择城市", trigger: "change,blur" }
        ],
        name: [{ required: true, message: "请输入区域名称", trigger: "blur" }],
        seq: [{ required: true, message: "请输入角色code", trigger: "blur" }]
      }
    };
  },
  filters: {
    filterstatus(val) {
      if (val == 1) {
        return "有效";
      } else {
        return "无效";
      }
    },
    filtercityId(val) {
      if (val == 1) {
        return "武汉";
      } else if (val == 2) {
        return "长沙";
      }
    }
  },
  watch: {
    centerDialogVisible1: function() {
      if (this.centerDialogVisible1) {
      } else {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
        this.actuserobj = {};
      }
    },
    centerDialogVisible2: function() {
      if (this.centerDialogVisible2) {
      } else {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
        this.actuserobj = {};
      }
    },
    DialogVisible: function() {
      if (!this.DialogVisible) {
        this.hotel_pageNum = 1;
      }
    },
    checkList: function() {
      this.polling();
    }
  },
  methods: {
    //获取列表数据
    getarea(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url = "area?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      if (this.areaName) {
        _Url += "&name=" + this.areaName;
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.areaName = "";
        this.total = res.data.total;
        if (res.data.total < 1) {
          this.$message("没有查询到相关数据信息");
          this.arealist = [];
        } else {
          let _data = res.data.data;
          for (let i in _data) {
            if (_data[i].status == 1) {
              _data[i].status = true;
            } else {
              _data[i].status = false;
            }
          }
          this.arealist = _data;
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getarea();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getarea();
    },
    //酒店列表翻页
    hotelhandleCurrentChange(val) {
      this.hotel_pageNum = val;
      this.confirmEdit(1);
    },
    //酒店列表修改每页请求数据条数
    hotelhandleSizeChange(val) {
      this.hotel_pageSize = val;
      this.confirmEdit(1);
    },
    //打开编辑弹框
    openDialog(val) {
      this.disval = val;
      if (val == 1) {
        this.centerDialogVisible1 = true;
      } else if (val == 2) {
        this.centerDialogVisible2 = true;
      }
    },
    //点击编辑某条数据
    handleedit(obj) {
      // this.ruleForm = obj;
      for (let key in obj) {
        if (obj[key]) {
          this.ruleForm[key] = obj[key];
        }
      }
      this.actuserobj = obj;
      this.getidinfo(obj.id);
      this.openDialog(2);
    },
    //轮询
    polling() {
      if (this.checkList.length) {
        for (let j in this.oldhotellist) {
          for (let i in this.checkList) {
            if (this.checkList[i] === this.oldhotellist[j].id) {
              this.oldhotellist[j].status = 1;
            } else {
              this.oldhotellist[j].status = 0;
            }
          }
        }
      } else {
        this.oldhotellist.map(value => {
          value.status = 0;
        });
      }
    },
    //分配酒店
    distribution(obj) {
      this.ruleobj = obj;
      let _Url =
          "area/hotel?pageNum=" +
          this.hotel_pageNum +
          "&pageSize=" +
          this.hotel_pageSize,
        _parms = {
          areaId: obj.id,
          cityId: obj.cityId
        };
      for (let key in _parms) {
        if (_parms[key]) {
          _Url += "&" + key + "=" + _parms[key];
        }
      }
      this.$http.get(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.DialogVisible = true;
          let _data = { ...res.data };
          for (let j in _data.data) {
            if (_data.data[j].status == 1) {
              this.checkList.push(_data.data[j].id);
            }
          }
          this.hotellist = _data.data;
          this.oldhotellist = JSON.parse(JSON.stringify(_data.data));
          this.oldhotellist.map(value => {
            delete value.name;
            value.status = value.status ? value.status : 0;
          });
          this.hoteltoal = _data.total;
        }
      });
    },
    //保存修改酒店
    confirmEdit(val) {
      // if (this.checkList.length) {
        let _parms = {
          areaId: this.ruleobj.id,
          hotelList: this.oldhotellist
        };
        this.$http.put("area/updateHotel", _parms).then(res => {
          if (res.status == 200) {
            this.checkList = [];
            this.oldhotellist = [];
            if (val == 1) {
              this.distribution(this.ruleobj);
            } else {
              this.$message.success("保存成功！");
              this.DialogVisible = false;
            }
          }
        });
      // } else {
      //   this.checkList = [];
      //   this.oldhotellist = [];
      //   this.distribution(this.ruleobj);
      // }
    },
    //修改用户状态
    changestatus(obj) {
      let _Url = "area/status",
        _parms = {
          status: "",
          id: obj.id
        };
      if (obj.status) {
        _parms.status = 1;
      } else {
        _parms.status = 0;
      }
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
        }
      });
    },
    //通过ID查询某个数据信息
    getidinfo(id) {
      this.$http.get("area/" + id).then(res => {
        let obj = res.data;
        obj.remark = obj.remark ? obj.remark : "";
        for (let key in obj) {
          // if (obj[key]) {
          this.ruleForm[key] = obj[key];
          // }
        }
      });
    },
    //保存
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //重置
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
        this.ruleForm.remark = "";
      }
    },
    //保存编辑
    savedata() {
      let _Url = "area",
        _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key] !== "") {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible1 = !this.centerDialogVisible1;
            this.getarea();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible2 = !this.centerDialogVisible2;
            this.getarea();
          }
        });
      }
    }
  },
  created() {
    this.getarea();
  }
};
</script>
<style lang="less" scoped>
.area_list {
  .topleft {
    .el-input {
      width: 70%;
    }
  }
  .dialogpag {
    .el-pagination {
      margin-top: 40px;
      width:100%;
    }
  }
  .el-checkbox-group {
    width: 100%;
    font-size:26px;
    .el-checkbox {
      width: 20%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>




