

<template>
  <div class="supplement mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="term.goodsName"
          placeholder="请输入商品名称"
          @keyup.enter.native="getreplenisherlist(1)"
          clearable
        ></el-input>
        <el-input
          v-model="term.operator"
          placeholder="请输入操作人姓名"
          @keyup.enter.native="getreplenisherlist(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getoperatorlist(1)">搜索</el-button>
      </div>
    </div>

    <el-table
      :data="operatorlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="hotelName" align="center" label="酒店名称"></el-table-column>
      <el-table-column prop="goodsName" align="center" label="商品名称"></el-table-column>
      <el-table-column prop="equipNO" align="center" label="设备编号"></el-table-column>
      <el-table-column prop="channelNo" align="center" label="货道号"></el-table-column>
      <el-table-column prop="num" align="center" label="数量"></el-table-column>
      <el-table-column prop="position" align="center" label="位置"></el-table-column>
      <el-table-column prop="operator" align="center" label="操作人"></el-table-column>
      <el-table-column prop="createTime" align="center" label="时间"></el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
  </div>
</template>

<script>
export default {
  name: "supplement",
  data() {
    return {
      total: 0,
      pageNum: 1,
      pageSize: 10,
      supplierName: "",
      ruleForm: {
        areaId: ""
      },
      term: {
        goodsName: "",
        operator: ""
      },
      operatorlist: [],
      arealist: []
    };
  },

  filters: {
    fiterroleId: function(val) {
      if (val == 1) {
        return "运营";
      } else if (val == 2) {
        return "厂商";
      }
    }
  },
  methods: {
    //获取列表数据
    getoperatorlist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "operationLog3?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      for (let key in this.term) {
        if (this.term[key] || this.term[key] == "0") {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.total = res.data.total;
        if (res.data.total < 1) {
          this.$message("没有查询到相关数据信息");
          this.operatorlist = [];
        } else {
          let _data = res.data.data;
          for (let i in _data) {
            if (_data[i].status == 1) {
              _data[i].status = true;
            } else {
              _data[i].status = false;
            }
            _data[i].createTime = "20" + _data[i].createTime;
          }
          this.operatorlist = _data;
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getoperatorlist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getoperatorlist();
    }
  },
  created() {
    this.getoperatorlist();
  }
};
</script>

<style lang="less">
.supplement {
  .topleft {
    .el-input {
      width: 40%;
    }
  }
}
</style>




