<template>
  <div class="shenzhen mainboss">
    <div class="equipNOinput">
      <el-form ref="form" label-width="100px">
        <el-form-item label=" 设备编号：">
          <el-input
            v-model="equipNO"
            placeholder="请输入设备编号"
            @keyup.enter.native="handequip"
            clearable
          ></el-input>
        </el-form-item>
      </el-form>

      <el-button type="primary" @click="handequip">生成二维码</el-button>

      <el-dialog
        v-if="centerDialogVisible"
        title="设备二维码"
        :visible.sync="centerDialogVisible"
        :modal-append-to-body="false"
        top="3%"
        width="20%"
        center
      >
        <div v-show="isqrcode" class="qrbox">
          <div class="qrimg imageWrapper" ref="imageWrapper" id="imageWrapper">
            <div id="qrcode" ref="qrcode" @contextmenu.prevent="printqr()"></div>
            <slot>{{equipNO}}</slot>
          </div>
          <el-button type="primary" class="doloadbut" @click="downloadImg">下载二维码</el-button>
          <el-button type="primary" class="doloadbut" @click="printqr">打印二维码</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import QRCode from "qrcodejs2";
import html2canvas from "html2canvas";
export default {
  name: "shenzhen",
  data() {
    return {
      equipNO: "",
      isqrcode: false,
      centerDialogVisible: false
    };
  },
  watch: {
    equipNO: function() {
      if (this.equipNO.length == 0) {
        this.isqrcode = false;
        this.centerDialogVisible = false;
      }
    }
  },
  methods: {
    handequip() {
      console.log("equipNO:", this.equipNO);
      let _this = this;

      if (this.equipNO) {
        this.isqrcode = true;
        this.centerDialogVisible = true;
        setTimeout(() => {
          let qrcodeEL = document.getElementById("qrcode");
          let qrcode = new QRCode(qrcodeEL);
          document.getElementById("qrcode").innerHTML = "";
          _this.qrcode();
        }, 50);
      } else {
        this.$message("请输入设备编号");
      }
    },
    // 生成二维码
    qrcode() {
      let text = "";
      text =
        this.$GLOBAL.qrAPI + "index3.html?no=" + this.equipNO + "&type=test"; // 设置二维码内容或跳转地址
      let qrcode = new QRCode("qrcode", {
        width: 114, // 设置宽度，单位像素
        height: 114, // 设置高度，单位像素
        text: text
      });
    },
    //下载设备二维码
    downloadImg() {
      html2canvas(document.querySelector("#imageWrapper")).then(canvas => {
        let dataURL = canvas.toDataURL("image/png");
        let a = document.createElement("a");
        let event = new MouseEvent("click");
        // 下载图名字
        a.download = this.equipNO;
        //url
        a.href = dataURL;
        //合成函数，执行下载
        a.dispatchEvent(event);
      });
    },
    //打印二维码
    printqr() {
      let subOutputRankPrint = document.getElementById("imageWrapper");
      let newContent = subOutputRankPrint.innerHTML;
      let oldContent = document.body.innerHTML;
      document.body.innerHTML = newContent;
      document.title = "";

      // if (!!window.ActiveXObject || "ActiveXObject" in window) {
      //   this.remove_ie_header_and_footer();
      // }
      window.print();
      window.location.reload();
      document.body.innerHTML = oldContent;
      return false;
    },
    // 设置页眉页脚为空
    remove_ie_header_and_footer() {
      var hkey_root, hkey_path, hkey_key;
      hkey_path =
        "HKEY_CURRENT_USER\\Software\\Microsoft\\Internet Explorer\\PageSetup\\";
      try {
        var RegWsh = new ActiveXObject("WScript.Shell");
        RegWsh.RegWrite(hkey_path + "header", "");
        RegWsh.RegWrite(hkey_path + "footer", "");
      } catch (e) {}
    }
  },
  components: {}
};
</script>

<style lang="less">
.shenzhen {
  width: 100%;
  height: 1005;
  .equipNOinput {
    width: 400px;
  }
  .qrimg {
    #qrcode {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    width: 140px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 20px;
    padding: 10px 5px;
    border: 1px solid pink;
  }
  .qrbox {
    font-size: 14px;
  }
  .doloadbut {
    margin-left: 10%;
    float: left;
  }
  .el-dialog--center {
    min-width: 380px;
    height: 350px;
  }
}
</style>




