<template>
  <div class="FirstEquipList mainboss">
    <div class="martop">
      <div class="topleft">
        <!-- <el-input
          v-model="term.name"
          placeholder="设备名称"
          @keyup.enter.native="getequiplist(1)"
          clearable
        ></el-input>-->
        <el-input
          v-model="term.serialNo"
          placeholder="请输入设备编号"
          @keyup.enter.native="getequiplist(1)"
          clearable
        ></el-input>
        <!-- <el-select v-model="term.status" clearable filterable placeholder="请选择设备状态">
          <el-option
            v-for="item in statuslist"
            :key="item.value"
            :label="item.label"
            :value="item.id"
          ></el-option>
        </el-select>-->
        <el-button type="primary" @click="getequiplist(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="equiplist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="serialNo" align="center" label="设备编号"></el-table-column>
      <el-table-column align="center" label="类型">
        <template slot-scope="scope">{{scope.row.type1=='1'?'1号机':scope.row.type1=='3'?'号机':''}}</template>
      </el-table-column>
      <el-table-column align="center" label="状态">
        <template slot-scope="scope">{{scope.row.status | fiterstatus}}</template>
      </el-table-column>
      <el-table-column prop="updateTime" align="center" label="更新时间"></el-table-column>
      <el-table-column prop="createTime" align="center" label="注册时间"></el-table-column>
      <el-table-column align="center" label="休眠">
        <template slot-scope="scope">{{scope.row.sleepFlag=='1'?'是':''}}</template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="300">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="centerDialogVisible"
      title="编辑设备"
      :visible.sync="centerDialogVisible"
      top="1%"
      :modal-append-to-body="false"
      width="70%"
      center
      append-to-body
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <fieldset style="border: 1px solid pink;">
          <legend>设备信息</legend>
          <el-form-item label="设备编号" required>
            <el-col :span="6">
              <el-form-item prop="serialNo">
                <el-input
                  v-model="ruleForm.serialNo"
                  placeholder="请输入设备编号"
                  oninput="if(value.length>64)value=value.slice(0,64)"
                  clearable
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item label="设备类型" prop="type">
                <el-select v-model="ruleForm.type" placeholder="请选择设备类型">
                  <el-option
                    v-for="item in device"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item label="设备状态">
                <el-select v-model="ruleForm.status" placeholder="请选择设备状态">
                  <el-option
                    v-for="item in statuslist"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id"
                    :disabled="item.disabled"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="所在酒店" required>
            <el-col :span="6">
              <el-form-item label prop="hotelId">
                <el-cascader
                  :placeholder="ruleForm.hotelName?ruleForm.hotelName:'选择所在酒店'"
                  :show-all-levels="false"
                  :options="options"
                  filterable
                  clearable
                  @active-item-change="handleItemChange"
                  @change="handlechange"
                  @focus="getarea"
                ></el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item label="所在位置">
                <el-input v-model="ruleForm.roomNo" placeholder="请输入所在位置" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item label="供电方式">
                <el-select v-model="ruleForm.powerType" placeholder="请选择供电方式">
                  <el-option
                    v-for="item in powerlist"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id"
                    :disabled="item.disabled"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="联网方式">
            <el-col :span="6">
              <el-form-item label>
                <el-select v-model="ruleForm.netType" clearable filterable placeholder="请选择联网方式">
                  <el-option
                    v-for="item in netlist"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="备注">
            <el-input
              v-model="ruleForm.remark"
              type="textarea"
              :rows="2"
              placeholder="请输入备注"
              maxlength="500"
              clearable
            ></el-input>
          </el-form-item>
        </fieldset>

        <fieldset style="border: 1px solid pink;" v-show="ruleForm.type">
          <legend>货道信息</legend>
          <el-button
            style="margin: 10px 33px;"
            type="primary"
            icon="el-icon-plus"
            @click="copygoods()"
          >复制货道信息</el-button>
          <el-form-item label="货道编号" v-for="(item,index) in ruleForm.channelList" :key="index">
            <el-col :span="3">
              <el-form-item>
                <el-input
                  v-model="item.serialNo"
                  placeholder="请输入设备编号"
                  type="number"
                  :disabled="true"
                  clearable
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item
                label="货道商品"
                :rules="newrules.cheackgoods"
                :prop="'channelList.' + index +'.goodsId'"
              >
                <el-select
                  v-if="ruleForm.type == 1"
                  v-model="item.goodsName"
                  clearable
                  filterable
                  placeholder="请选择商品"
                  @change="businessTypeChange"
                  @focus="getgoods(index)"
                >
                  <el-option
                    v-for="item in goodslist"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
                <el-cascader
                  v-if="ruleForm.type ==3"
                  :placeholder="item.goodsName?item.goodsName:'请选择类别商品'"
                  :show-all-levels="false"
                  :options="optionstype"
                  filterable
                  clearable
                  @active-item-change="handleItemChangetype"
                  @change="handlechangetype"
                  @focus="getgoodstype(index)"
                ></el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="货道状态">
                <el-switch
                  v-model="item.status"
                  @change="changestatus(item)"
                  active-text
                  inactive-text
                ></el-switch>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item
                label="货道库存"
                :rules="newrules.cheackstock"
                :prop="'channelList.' + index +'.stock'"
              >
                <el-input
                  v-model="item.stock"
                  placeholder="请选择货道库存"
                  clearable
                  @keyup.native="proving(item)"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>
        </fieldset>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
      <el-dialog
        width="30%"
        title="设备编号"
        center
        :visible.sync="innerVisible"
        top="2vh"
        append-to-body
      >
        <el-form
          :model="Formedit"
          :rules="rules"
          ref="Formedit"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="设备编号" prop="serialNo">
            <el-input
              v-model="Formedit.serialNo"
              placeholder="请输入设备编号"
              oninput="if(value.length>64)value=value.slice(0,64)"
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('Formedit')">确认保存</el-button>
            <el-button @click="resetForm('Formedit')">表单重置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "FirstEquipList",
  data() {
    return {
      total: 0,
      pageNum: 1,
      pageSize: 15,
      loading: false,
      innerVisible: false,
      centerDialogVisible: false,
      term: {
        name: "",
        serialNo: "",
        status: ""
      },
      Formedit: {
        serialNo: "",
        name: ""
      },
      ruleForm: {
        hotelId: "",
        id: "",
        name: "",
        remark: "",
        roomId: "",
        serialNo: "",
        status: "1",
        type: "",
        netType: "",
        powerType: "",
        channelList: []
      },
      actuserobj: {},
      rules: {
        name: [{ required: true, message: "请输入设备名称", trigger: "blur" }],
        serialNo: [
          { required: true, message: "请输入设备编号", trigger: "blur" }
        ],
        roomNo: [
          { required: true, message: "请输入所在位置", trigger: "blur" }
        ],
        roomId: [
          { required: true, message: "请选择所在位置", trigger: "change" }
        ],
        hotelId: [
          { required: true, message: "请选择酒店名称", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择设备状态", trigger: "change" }
        ],
        type: [
          { required: true, message: "请选择设备类型", trigger: "change" }
        ],
        netType: [
          { required: true, message: "请选择联网方式", trigger: "change" }
        ],
        powerType: [
          { required: true, message: "请选择供电方式", trigger: "change" }
        ],
        goodsId: [
          { required: true, message: "请选择货道商品", trigger: "change" }
        ],
        channstatus: [
          { required: true, message: "请选择货道状态", trigger: "change" }
        ]
      },
      newrules: {
        cheackgoods: [
          {
            required: true,
            message: "请选择货道商品",
            trigger: ["blur", "change"]
          }
        ],
        cheackstock: [
          {
            required: true,
            message: "请选择库存",
            trigger: ["blur", "change"]
          }
        ]
      },
      netlist: [
        {
          id: 1,
          label: "物联卡"
        },
        {
          id: 2,
          label: "wifi"
        }
      ],
      powerlist: [
        {
          id: 1,
          label: "电池"
        },
        {
          id: 2,
          label: "插电"
        }
      ],
      device: [
        {
          id: 1,
          label: "1号机（10货道）"
        },
        {
          id: 3,
          label: "3号机（3货道）"
        }
      ],
      equiplist: [],
      options: [],
      goodslist: [],
      optionstype: [],
      statuslist: [
        {
          id: 1,
          label: "离线"
        },
        {
          id: 2,
          label: "在线"
        },
        {
          id: 3,
          label: "停用"
        }
      ]
    };
  },
  watch: {
    centerDialogVisible: function() {
      if (!this.centerDialogVisible2) {
        for (let key in this.ruleForm) {
          if (this.ruleForm[key]) {
            this.ruleForm[key] = "";
          }
        }
        this.ruleForm.channelList = [];
        this.goodslist = [];
        this.actuserobj = {};
      }
    },
    innerVisible: function() {
      if (!this.DialogVisible) {
        for (let key in this.Formedit) {
          this.Formedit[key] = "";
        }
      }
    },
    "ruleForm.type": {
      handler(value) {
        if (value) {
          if (this.centerDialogVisible1) {
            this.setchannelList(value);
          }
        } else {
          this.ruleForm.channelList = [];
        }
      },
      deep: true
    },
    "ruleForm.hotelId": {
      handler(value) {
        if (value) {
          this.ispostion = true;
        } else {
          this.ispostion = false;
        }
      }
    }
  },
  filters: {
    fiterstatus: function(val) {
      if (val == 1) {
        return "离线";
      } else if (val == 2) {
        return "在线";
      } else if (val == 3) {
        return "停用";
      }
    }
  },
  methods: {
    //查询区域下拉列表
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("equip/areaList").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.options = _data;
        });
      }
    },
    //查询酒店下拉列表
    handleItemChange(val) {
      let _this = this;
      this.$http.get("equip/hotelList/" + val[0]).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (val[0] == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    //查询商品列表
    getgoods(index) {
      this.Indnum = index;
      if (this.ruleForm.hotelId) {
        if (this.goodslist.length < 1) {
          let _url = "equip/goods?hotelId=" + this.ruleForm.hotelId;
          this.$http.get(_url).then(res => {
            this.goodslist = res.data;
          });
        }
      } else {
        this.$message.error("请先选择酒店");
      }
    },
    handlechange(val) {
      this.ruleForm.hotelId = val[1];
      console.log("this.ruleForm.hotelId:", this.ruleForm.hotelId);
    },
    businessTypeChange(item) {
      this.ruleForm.channelList[this.Indnum].goodsId = item;
    },
    //查询某类别下商品
    handleItemChangetype(val) {
      this.$http.get("equip/goods3?typeId=" + val[0]).then(res => {
        if (res.status == 200) {
          let _datas = [];
          for (let i in this.optionstype) {
            if (val[0] == this.optionstype[i].value) {
              for (let j in res.data) {
                let _obj = {};
                _obj.value = res.data[j].id;
                _obj.label = res.data[j].name;
                _obj.salePrice = res.data[j].salePrice;
                _datas.push(_obj);
              }
              this.optionstype[i].children = _datas;
            }
          }
        }
      });
    },
    //查询列表数据
    getequiplist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "equipInitial?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        let _data = [...res.data.data];
        _data.map(value => {
          value.updateTime = value.updateTime ? "20" + value.updateTime : "";
          value.createTime = value.createTime ? "20" + value.createTime : "";
        });
        this.equiplist = _data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getequiplist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getequiplist();
    },
    //打开编辑弹框
    openDialog(val) {
      this.centerDialogVisible = !this.centerDialogVisible;
    },
    //点击编辑某条数据
    handleedit(obj) {
      this.getidinfo(obj.id);
      this.openDialog();
      this.actuserobj = obj;
    },
    //通过ID查询某个数据信息
    getidinfo(id) {
      this.$http.get("equip/" + id).then(res => {
        let obj = { ...res.data };
        this.actuserobj.children = obj;
        obj.type = Number(obj.type);
        // obj.position = obj.roomNo?obj.roomNo:'';
        for (let i in obj.channelList) {
          obj.channelList[i].status =
            obj.channelList[i].status == 1 ? true : false;
          obj.channelList[i].serialNo = Number(obj.channelList[i].serialNo);
          obj.channelList[i].stock = Number(obj.channelList[i].stock);
        }
        obj.channelList.sort(function(a, b) {
          if (a.serialNo < b.serialNo) {
            return -1;
          } else if (a.serialNo > b.serialNo) {
            return 1;
          }
          return 0;
        });
        obj.remark = obj.remark ? obj.remark : "";
        this.ruleForm = JSON.parse(JSON.stringify(obj));
        console.log("ruleForm:", this.ruleForm);
      });
    },
    //修改货道状态
    changestatus(obj) {
      let _Url = "equip/channel/status",
        _parms = {},
        _this = this;
      obj.status = obj.status ? 1 : 2;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          for (let i in _this.ruleForm.channelList) {
            if (obj.id == _this.ruleForm.channelList[i].id) {
              _this.ruleForm.channelList[i].status =
                obj.status == 1 ? true : false;
            }
          }
          this.$message.success("修改成功");
        }
      });
    },
    //输入整数
    proving(obj) {
      obj.stock = obj.stock.replace(/[^\.\d]/g, "");
      obj.stock = obj.stock.replace(".", "");
    },
    //点击确认按钮，进行表单校验
    submitForm(formName) {
      if (this.ruleForm.roomNo) {
        this.ruleForm.position = this.ruleForm.roomNo;
      }
      if (this.centerDialogVisible1) {
        if (!this.ruleForm.roomNo) {
          this.$message.error("请输入所在位置");
          return;
        }
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (formName == "Formedit") {
            if (this.innerVisible) {
              this.copygoods(this.Formedit.serialNo);
            }
          } else if (formName == "ruleForm") {
            this.savedata();
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //重置
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
        this.ruleForm.remark = "";
      }
    },
    //保存修改编辑
    savedata() {
      let _Url = "equip",
        _channelList = [],
        _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.ruleForm.channelList.length > 0) {
        let _List = this.ruleForm.channelList;
        for (let i in _List) {
          let _obj = {
            id: _List[i].id,
            goodsId: _List[i].goodsId,
            stock: _List[i].stock
          };
          _channelList.push(_obj);
        }
      }
      _parms.channelList = _channelList;

      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("保存成功");
          this.centerDialogVisible = !this.centerDialogVisible;
          this.getequiplist();
        }
      });
    },
    //查询设备商品
    copygoods(val) {
      if (val) {
        this.$http.get("equip/copyGoods?serialNo=" + val).then(res => {
          let _data = [...res.data];
          for (let i in _data) {
            for (let j in this.ruleForm.channelList) {
              if (
                Number(_data[i].serialNo) ==
                Number(this.ruleForm.channelList[j].serialNo)
              ) {
                this.ruleForm.channelList[j].goodsId = _data[i].goodsId;
                this.ruleForm.channelList[j].goodsName = _data[i].goodsName;
              }
            }
          }
          this.innerVisible = false;
        });
      } else {
        this.innerVisible = true;
      }
    }
  },
  created() {
    this.getequiplist();
  }
};
</script>

<style lang="less">
.FirstEquipList {
  .topleft {
    .el-input {
      width: 60%;
    }
  }
}
</style>


