<template>
  <div class="region mainboss">
    <div class="martop">
      <div class="topleft">
        <el-select v-model="term.cityId" clearable filterable placeholder="请选择城市">
          <el-option v-for="item in citys" :key="item.value" :label="item.name" :value="item.id"></el-option>
        </el-select>

        <!-- <el-date-picker v-model="term.month" type="monthrange" placeholder="月份" align="right"></el-date-picker> -->
        <el-date-picker v-model="term.month" type="month" placeholder="选择月"></el-date-picker>
        <el-button type="primary" @click="getlist(1)">搜索</el-button>
      </div>
    </div>

    <el-table
      :data="regionlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column align="center" label="城市">
        <template slot-scope="scope">{{scope.row.id | fiterid}}</template>
      </el-table-column>
      <el-table-column prop="hotelQuantity" align="center" label="酒店数"></el-table-column>
      <el-table-column prop="equipQuantity" align="center" label="设备数"></el-table-column>
      <el-table-column prop="amount" align="center" label="总销售额"></el-table-column>
      <el-table-column prop="avgAmount" align="center" label="台均销售"></el-table-column>
      <el-table-column prop="avgDayAmount" align="center" label="日台均销售"></el-table-column>
      <el-table-column fixed="right" label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            @click="handleDetails(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >详情</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="区域列表"
      :visible.sync="outerVisible"
      width="90%"
      top="5vh"
      center
      append-to-body
      :modal-append-to-body="false"
    >
      <el-table
        :data="arealist"
        :height="this.$store.state.tableHeight"
        resizable
        border
        style="width: 100%"
        v-loading="loading"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        element-loading-text="数据正在加载中"
      >
        <el-table-column prop="name" align="center" label="区域"></el-table-column>
        <el-table-column prop="hotelQuantity" align="center" label="酒店数"></el-table-column>
        <el-table-column prop="equipQuantity" align="center" label="设备数"></el-table-column>
        <el-table-column prop="amount" align="center" label="总销售额"></el-table-column>
        <el-table-column prop="avgAmount" align="center" label="台均销售"></el-table-column>
        <el-table-column prop="avgDayAmount" align="center" label="日台均销售"></el-table-column>
        <el-table-column fixed="right" label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              @click="handlehotel(scope.row)"
              type="primary"
              size="mini"
              icon="el-icon-edit"
            >详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        width="80%"
        title="酒店列表"
        center
        :visible.sync="innerVisible"
        top="2vh"
        append-to-body
      >
        <el-table
          :data="hotellist"
          :height="this.$store.state.tableHeight"
          resizable
          border
          style="width: 100%"
          v-loading="loading"
          element-loading-background="rgba(0, 0, 0, 0.5)"
          element-loading-text="数据正在加载中"
        >
          <el-table-column prop="name" align="center" label="酒店"></el-table-column>
          <el-table-column prop="equipQuantity" align="center" label="设备数"></el-table-column>
          <el-table-column prop="amount" align="center" label="总销售额"></el-table-column>
          <el-table-column prop="avgAmount" align="center" label="台均销售"></el-table-column>
          <el-table-column prop="avgDayAmount" align="center" label="日台均销售"></el-table-column>
        </el-table>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "region",
  data() {
    return {
      arealist: [],
      hotellist: [],
      regionlist: [],
      pageNum: 1,
      pageSize: 10,
      loading: false,
      outerVisible: false,
      innerVisible: false,
      citys: [
        {
          id: 1,
          name: "武汉"
        },
        {
          id: 2,
          name: "长沙"
        }
      ],
      term: {
        cityId: "",
        month: ""
      }
    };
  },
   filters: {
     fiterid:function(id){
       if(id==1){
         return '武汉'
       }else if(id==2){
         return '长沙'
       }
     }
   },
  created() {
    this.term.month = new Date();
    this.getlist();
  },
  methods: {
    //获取列表数据
    getlist(val) {
      let _Url = "avgCount/city?",
        _parms = {},
        _month = "";
      if (this.term && this.term.month) {
        let _mon = this.term.month.getMonth() + 1;
        let newmon = _mon < 10 ? "0" + _mon : _mon;
        _month = String(this.term.month.getFullYear()) + String(newmon);
      }
      _parms = {
        cityId: this.term.cityId,
        month: _month
      };

      for (let key in _parms) {
        if (_parms[key]) {
          _Url += key + "=" + _parms[key] + "&";
        }
      }
      _Url = _Url.substr(0, _Url.length - 1);
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.regionlist = res.data;
      });
    },
    //区域列表
    handleDetails(obj) {
      let _Url = "avgCount/area?",
        _parms = {},
        _month = "";
      if (this.term && this.term.month) {
        let _mon = this.term.month.getMonth() + 1;
        let newmon = _mon < 10 ? "0" + _mon : _mon;
        _month = String(this.term.month.getFullYear()) + String(newmon);
      }
      _parms = {
        cityId: this.term.cityId,
        month: _month,
        areaId: obj.id
      };

      for (let key in _parms) {
        if (_parms[key]) {
          _Url += key + "=" + _parms[key] + "&";
        }
      }
      _Url = _Url.substr(0, _Url.length - 1);
      this.$http.get(_Url).then(res => {
        this.outerVisible = true;
        this.arealist = res.data;
      });
    },
    //
    handlehotel(obj) {
      let _Url = "avgCount/hotel?",
        _parms = {},
        _month = "";
      if (this.term && this.term.month) {
        let _mon = this.term.month.getMonth() + 1;
        let newmon = _mon < 10 ? "0" + _mon : _mon;
        _month = String(this.term.month.getFullYear()) + String(newmon);
      }
      _parms = {
        cityId: this.term.cityId,
        month: _month,
        areaId: obj.id
      };
      for (let key in _parms) {
        if (_parms[key]) {
          _Url += key + "=" + _parms[key] + "&";
        }
      }
      _Url = _Url.substr(0, _Url.length - 1);
      this.$http.get(_Url).then(res => {
        this.innerVisible = true;
        this.hotellist = res.data;
      });
    }
  }
};
</script>

<style>
</style>


