<template>
  <div class="performance mainboss">
    <div class="martop">
      <div class="topleft">
        <el-date-picker v-model="term.startTime" type="datetime" placeholder="开始时间" align="right" :class="userinfo.type =='2'?'jd-input':''"></el-date-picker>
        <el-date-picker v-model="term.endTime" type="datetime" placeholder="结束时间" align="right" :class="userinfo.type =='2'?'jd-input':''"></el-date-picker>
        <el-button type="primary" @click="getsales(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="saleslist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="name" align="center" label="姓名"></el-table-column>
      <el-table-column prop="amount" align="center" label="业绩(元)"></el-table-column>
      <el-table-column prop="phone" align="center" label="联系电话"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "performance",
  data() {
    return {
      userinfo: {},
      total: 0,
      ptotal: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      term: {
        startTime: "",
        endTime: ""
      },
      options: [],
      arealist: [],
      saleslist: []
    };
  },
  methods: {
    //查询区域下拉列表
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("sales/areaList").then(res => {
          let _data = [];
          this.arealist = res.data;
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.options = _data;
        });
      }
    },
    handleItemChange(val) {
      this.gethotel(val[0]);
    },
    //查询酒店下拉列表
    gethotel(areaId) {
      this.$http.get("sales/hotelList/" + areaId).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (areaId == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    handlechange(val) {
      this.term.hotelId = val[1];
    },
    //获取列表数据
    getsales(val) {
      let _parms = {},
        _Url = "",
        _this = this;
      if (val == 1) {
        this.pageNum = 1;
      }
      if (this.term.startTime && this.term.endTime) {
        let _startTime = Number(this.term.startTime.getTime());
        let _endTime = Number(this.term.endTime.getTime());
        if (_startTime > _endTime) {
          this.term.endTime = "";
          this.$message.error("开始时间不得大于结束时间");
          return;
        } else if (_endTime - _startTime > 86400000 * 90) {
          this.term.endTime = "";
          this.$message.error("开始时间与结束时间间隔不得大于90天");
          return;
        }
      } else {
        this.$message("请选择时间");
        return;
      }

      if (this.term.startTime) {
        _parms.startTime = this.Until.changDage(this.term.startTime);
      }
      if (this.term.endTime) {
        _parms.endTime = this.Until.changDage(this.term.endTime);
      }
      _parms.areaId = this.term.areaId;
      _parms.hotelName = this.term.hotelName;
      _Url = "brushCount?";
      let _par="";
      for (let key in _parms) {
        if (_parms[key]) {
           _par+= "&" + key + "=" + _parms[key];
        }
      }
      _par = _par.substr(1);
      _Url+=_par;
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.total = res.total;
        if (res.data.total < 1) {
          this.$message("没有查询到相关数据信息");
          this.saleslist = [];
        } else {
          let obj = {
            amount: 0,
            name: "本页合计",
            quantity: 0
          };
          for (let i = 0; i < res.data.length; i++) {
            obj.amount = obj.amount + res.data[i].amount * 100;
            obj.quantity += res.data[i].quantity;
          }
          obj.amount = obj.amount / 100;
          setTimeout(() => {
            res.data.unshift(obj);
            _this.saleslist = res.data;
          }, 200);
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getsales();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getsales();
    }
  },
  created() {
    this.userinfo = this.$store.state.userInfo;
    const startTime = new Date();
    startTime.setTime(startTime.getTime() - 3600 * 1000 * 24 * 90+60);
    this.term.startTime = startTime;

    const endTime = new Date();
    endTime.setTime(endTime.getTime());
    this.term.endTime = endTime;
    this.getsales();
  }
};
</script>
<style lang="less">
.performance {
  .topleft {
    .el-input {
      width: 40%;
    }
    .jd-input{
      width: 40%;
    }
  }
}
</style>



