
<template>
  <div class="money_log mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="term.trueName"
          placeholder="请输入操作人姓名"
          @keyup.enter.native="getloglist(1)"
          clearable
        ></el-input>
        <el-cascader
          placeholder="选择区域酒店"
          :show-all-levels="false"
          :options="options"
          filterable
          clearable
          @active-item-change="handleItemChange"
          @change="handlechange"
          @focus="getarea"
        ></el-cascader>
        <el-button type="primary" @click="getloglist(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="loglist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="hotelName" align="center" label="酒店名称"></el-table-column>
      <el-table-column prop="roomNo" align="center" label="房间号"></el-table-column>
      <el-table-column prop="equipNo" align="center" label="设备号"></el-table-column>
      <el-table-column prop="amount" align="center" label="红包金额(元)"></el-table-column>
      <el-table-column prop="createTime" align="center" label="操作时间"></el-table-column>
      <el-table-column prop="trueName" align="center" label="操作人"></el-table-column>
      <el-table-column prop="username" align="center" label="联系电话"></el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
  </div>
</template>

<script>
export default {
  name: "money_log",
  data() {
    return {
      pageNum: 1,
      pageSize: 10,
      total: 0,
      loading: false,
      loglist: [],
      options: [],
      term: {
        trueName: "",
        hotelId: ""
      }
    };
  },

  methods: {
    //查询区域下拉列表
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("bonusLog/areaList").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.options = _data;
        });
      }
    },
    handleItemChange(val) {
      this.gethotel(val[0]);
    },
    //查询酒店下拉列表
    gethotel(areaId) {
      this.$http.get("bonusLog/hotelList/" + areaId).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (areaId == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    handlechange(val) {
      console.log("val:", val);
      this.term.hotelId = val[1];
    },
    //查询补货日志数据
    getloglist(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "/bonusLog?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        if (res.status == 200) {
          let _data = [...res.data.data];
          _data.map(value => {
            value.createTime = "20" + value.createTime;
          });
          this.loglist = _data;
          this.total = res.data.total;
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getloglist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getloglist();
    }
  },
  created() {
    this.getloglist();
  }
};
</script>

<style lang="less">
.money_log {
  .topleft {
    .el-input {
      width: 40%;
    }
  }
}
</style>





