
<template>
  <div class="operation_role mainboss">
    <div class="martop">
      <div class="topleft"></div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建角色</el-button>
      </div>
    </div>

    <el-table
      :data="rolelist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
      style="width: 100%"
    >
      <el-table-column prop="name" align="center" label="角色名称"></el-table-column>
      <el-table-column align="center" label="角色状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="remark" align="center" label="角色说明"></el-table-column>
      <el-table-column prop="code" align="center" label="角色code"></el-table-column>
      <el-table-column fixed="right" label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
          <el-button
            @click="handlemenu(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-tickets"
          >菜单</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="centerDialogVisible"
      :title="disval==1?'创建角色':'编辑角色'"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
       
        <el-form-item label="角色名称" prop="name">
          <el-input
            v-model="ruleForm.name"
            maxlength="255"
            clearable
            :disabled="disval==1?false:true"
          ></el-input>
        </el-form-item>
        <el-form-item label="角色说明" prop="remark">
          <el-input v-model="ruleForm.remark" maxlength="255" clearable></el-input>
        </el-form-item>
        <el-form-item label="角色code" prop="code">
          <el-input v-model="ruleForm.code" maxlength="255" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="分配权限"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <ul class="diaul">
        <li v-for="item in menulist" :key="item.id">
          <el-checkbox v-model="item.status" :label="item.status">{{item.name}}</el-checkbox>
        </li>
        <el-button type="primary" @click="saverole">保存</el-button>
      </ul>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "operation_role",
  data() {
    return {
      rolelist: [],
      menulist: [],
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      DialogVisible: false,
      centerDialogVisible: false,
      actuserobj: {},
      ruleForm: {
        id: "",
        name: "",
        remark: "",
        code: "",
        status: ""
      },
      rules: {
        name: [{ required: true, message: "请输入角色名称", trigger: "blur" }],
        code: [{ required: true, message: "请输入角色id", trigger: "blur" }],
        remark: [{ required: true, message: "请输入角色说明", trigger: "blur" }]
      }
    };
  },
  methods: {
    //获取列表数据
    getlist() {
      let _Url =
        "operatorRole?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.total = res.data.total;
        if (res.data.total < 1) {
          this.$message("没有查询到相关数据信息");
          this.rolelist = [];
        } else {
          let _data = [...res.data.data];
          for (let i in _data) {
            if (_data[i].status == 1) {
              _data[i].status = true;
            } else if (_data[i].status == 2) {
              _data[i].status = false;
            }
          }
          this.rolelist = _data;
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getlist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getlist();
    },
    //显示弹框
    openDialog(val) {
      if (val == 1) {
        this.actuserobj = {};
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
      }
      this.disval = val ? val : "";
      this.centerDialogVisible = true;
    },
    //保存
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //重置
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
      }
    },
    //修改用户状态
    changestatus(obj) {
      let _Url = "operatorRole/status",
        _parms = {},
        _status = "";
      _status = obj.status ? 1 : 2;
      _parms = {
        status: _status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getlist();
        }
      });
    },
    //提交保存数据
    savedata() {
      let _Url = "operatorRole",
        _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getlist();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getlist();
          }
        });
      }
    },
    //点击编辑某条数据
    handleedit(obj) {
      this.actuserobj = obj;
      this.getidinfo(obj.id);
    },
    //点击菜单某条数据
    handlemenu(obj) {
      this.DialogVisible = true;
      this.actuserobj = obj;
      this.getrolemenu(obj);
    },
    //通过ID查询某个数据信息
    getidinfo(id) {
      this.$http.get("operatorRole/" + id).then(res => {
        if (res.status == 200) {
          this.openDialog(2);
          let obj = { ...res.data };
          obj.remark = obj.remark ? obj.remark : "";
          obj.code = obj.code ? obj.code : "";
          for (let key in obj) {
              this.ruleForm[key] = obj[key];
          }
        }
      });
    },
    //查询权限列表
    getrolemenu(obj) {
      this.$http.get("operatorRole/permission?id=" + obj.id).then(res => {
        if (res.status == 200) {
          let _data = [...res.data];
          for (let i in _data) {
            if (_data[i].status == 1) {
              _data[i].status = true;
            } else {
              _data[i].status = false;
            }
          }
          this.menulist = _data;
        }
      });
    },
    //保存用户权限
    saverole() {
      let role = [];
      for (let i in this.menulist) {
        if (this.menulist[i].status) {
          role.push(this.menulist[i].id);
        }
      }
      if (role.length < 1) {
        this.$message.error("请至少选择一个选项");
        return;
      }
      let _parms = {
        roleId: this.actuserobj.id,
        permission: role
      };

      this.$http.put("operatorRole/permission", _parms).then(res => {
        this.$message.success("保存成功");
        this.DialogVisible = !this.DialogVisible;
      });
    }
  },
  created() {
    this.getlist();
  }
};
</script>

<style lang="less" scoped>
.diaul {
  width: 70%;
  padding-left: 15%;
  li {
    margin: 5px;
  }
  button {
    margin: 20px auto;
  }
}
</style>


