

<template>
  <div class="record mainboss">
    <div class="martop">
      <div class="topleft">
        <el-cascader
          placeholder="选择区域酒店"
          :show-all-levels="false"
          :options="options"
          filterable
          clearable
          @active-item-change="handleItemChange"
          @change="handlechange"
          @focus="getarea"
        ></el-cascader>

        <el-cascader
          placeholder="选择类别商品"
          :show-all-levels="false"
          :options="types"
          filterable
          clearable
          @active-item-change="handlegoodsChange"
          @change="handlegoods"
          @focus="gettype"
        ></el-cascader>
        <el-select
          v-model="term.payway"
          clearable
          filterable
          placeholder="请选择支付方式"
          @blur="searchBlur"
        >
          <el-option
            v-for="item in paywaylist"
            :key="item.value"
            :label="item.label"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-button type="primary" @click="getrecord(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="recordlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="equipNo" align="center" label="设备编号"></el-table-column>
      <el-table-column prop="goodsName" align="center" label="商品名称"></el-table-column>
      <el-table-column prop="hotelName" align="center" label="酒店名称"></el-table-column>
      <el-table-column prop="position" align="center" label="所在位置"></el-table-column>
      <el-table-column prop="createTime" align="center" label="销售时间">
        <template slot-scope="scope">20{{scope.row.createTime}}</template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
  </div>
</template>
<script>
export default {
  name: "record",
  data() {
    return {
      total: 0,
      ptotal: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      term: {
        goodsId: "",
        payway:"",
        hotelId: ""
      },
      types: [],
      options: [],
      recordlist: [],
      paywaylist: [
        {
          id: 3,
          label: "微信支付"
        },
        {
          id: 2,
          label: "支付宝支付"
        }
      ]
    };
  },
  methods: {
    //隐藏支付方式选项
    searchBlur(e) {
      this.term.payway = e.target.value;
    },
    //查询区域下拉列表
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("orderDetail/area").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.options = _data;
        });
      }
    },
    //查询某一区域 下酒店列表
    handleItemChange(val) {
      this.$http.get("orderDetail/hotel?areaId=" + val[0]).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (val[0] == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    //选中某一酒店
    handlechange(val) {
      this.term.hotelId = val[1];
    },
    //查询类别下拉列表
    gettype() {
      if (this.types.length < 1) {
        this.$http.get("orderDetail/type").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.types = _data;
        });
      }
    },
    //查询某一类别下商品列表
    handlegoodsChange(val) {
      this.$http.get("orderDetail/goods?typeId=" + val[0]).then(res => {
        let _datas = [];
        for (let i in this.types) {
          if (val[0] == this.types[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.types[i].children = _datas;
          }
        }
      });
    },
    //选中某一商品
    handlegoods(val) {
      this.term.goodsId = val[1];
    },
    //获取列表数据
    getrecord(val) {
      if (val == 1) {
        this.pageNum = 1;
      }
      let _Url =
        "orderDetail?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        if (res.status == 200) {
          this.total = res.data.total;
          if (res.data.total < 1) {
            this.$message("没有查询到相关数据信息");
            this.recordlist = [];
          } else {
            this.recordlist = res.data.data;
          }
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getrecord();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getrecord();
    }
  },
  created() {
    this.getrecord();
  }
};
</script>
<style lang="less">
.record {
  .topleft {
    .el-input {
      width: 22%;
    }
  }
}
</style>




