import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/ready/login'
import Home from '@/components/system/homepage'
import Welcome from '@/components/system/welcome'
import role_list from '@/components/system/role/role_list'
import per_list from '@/components/system/permission/per_list'
import user_list from '@/components/system/user/user_list'
import area_list from '@/components/system/hotel/area/area_list'
import goods_list from '@/components/system/goods/goods_list'
import store_list from '@/components/system/goods/store/store_list'
import goods_category from '@/components/system/goods/category/goods_category'
import storelog_list from '@/components/system/goods/storelog/storelog_list'
import supplier_list from '@/components/system/goods/supplier/supplier_list'
import equip_list from '@/components/system/goods/equip/equip_list'
import FirstEquipList from '@/components/system/goods/equip/FirstEquipList'
import room_list from '@/components/system/hotel/room/room_list'
import hotel_list from '@/components/system/hotel/hotel_list'
import stock_list from '@/components/system/order/stock/stock_list'
import replenish_list from '@/components/system/apply/replenish/replenish_list'
import queryOrderSummary from '@/components/system/order/goods/queryOrderSummary'
import supply_list from '@/components/system/order/supply/supply_list'
import goods_lsit from '@/components/system/order/stock/goods_list'
import order_list from '@/components/system/order/goods/order_list'
import order_record from '@/components/system/order/goods/record'
import opendoor from '@/components/system/order/door/openDoor'
import getActivityList from '@/components/system/activity/getActivityList'
import apply_list from '@/components/system/apply/apply_list'
import apply_part from '@/components/system/apply/apply_part'
import apply_log from '@/components/system/apply/apply_log'
import apply_warning from '@/components/system/apply/apply_warning'
import operation from '@/components/system/operation/operation_list'
import operation_part from '@/components/system/operation/operation_part'
import operation_log from '@/components/system/operation/operation_log'
import operation_role from '@/components/system/operation/operation_role'
import operation_permission from '@/components/system/operation/operation_permission'
import Score from '@/components/system/statistical/score'
import Region from '@/components/system/statistical/region'
import SuperLog from '@/components/system/operation/superLog'
import Supplement from '@/components/system/operation/supplement'
import Money_log from '@/components/system/activity/money_log'
import Money_free from '@/components/system/activity/freeroom'
import stat_money from '@/components/system/statistical/money'
import Performance from '@/components/system/statistical/performance'
import Depot from '@/components/system/inventory/depot'
import StoreIn from '@/components/system/inventory/storeIn'
import Damage from '@/components/system/inventory/damage'
import GoodsCheck from '@/components/system/inventory/goodsCheck'
import StoreOut from '@/components/system/inventory/storeOut'
import Stock from '@/components/system/inventory/stock'
import Access from '@/components/system/inventory/access'
import Lack from '@/components/system/inventory/lack'
import Sales from '@/components/system/inventory/sales'
import Inventory from '@/components/system/purchase/inventory'
import Weaning from '@/components/system/purchase/weaning'
import Installed from '@/components/system/purchase/installed'
import Shenzhen from '@/components/system/factory/shenzhen'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', //登录页面
      name: 'Login',
      component: Login
    },{
      path: '/Home',  //系统页面
      name: 'Home',
      component: Home,
      children: [
        {
          path:"/",   
          component:Welcome
        },
        {
          path:"/role/list",   
          component:role_list
        },{
          path:"/permission/list",
          component:per_list
        },{
          path:"/user/list",
          component:user_list
        },{
          path:"/hotel/area/list",
          component:area_list
        },{
          path:"/goods/list",
          component:goods_list
        },{
          path:"/goods/store/list",
          component:store_list
        },{
          path:"/goods/category",
          component:goods_category
        },{
          path:"/goods/storelog/list",
          component:storelog_list
        },{
          path:"/goods/supplier/list",
          component:supplier_list   
        },{
          path:"/goods/equip/list",
          component:equip_list
        },{
          path:"/goods/equip/FirstEquipList",
          component:FirstEquipList
        },{
          path:"/hotel/room/list",
          component:room_list
        },{
          path:"/hotel/list",
          component:hotel_list  
        },{
          path:"/order/stock/list",
          component:stock_list
        },{
          path:"/apply/replenish/list",
          component:replenish_list
        },{
          path:"/order/door",
          component:opendoor
        },{
          path:"/order/goods/queryOrderSummary",
          component:queryOrderSummary
        },{
          path:"/order/supply/list",
          component:supply_list
        },{
          path:"/order/stock/goods",
          component:goods_lsit  
        },{
          path:"/order/goods/list",
          component:order_list
        },{
          path:"/order/goods/record",
          component:order_record
        },{
          path:"/activity/getActivityList",
          component:getActivityList
        },{
          path:"/apply/list",
          component:apply_list
        },{
          path:"/apply/part",
          component:apply_part
        },{
          path:"/apply/log",
          component:apply_log
        },{
          path:"/apply/warning",
          component:apply_warning
        },{
          path:"/operation/list",
          component:operation
        },{
          path:"/operation/part",
          component:operation_part
        },{
          path:"/operation/log",
          component:operation_log
        },{
          path:"/operation/role",
          component:operation_role
        },{
          path:"/operation/permission",
          component:operation_permission  
        },{
          path:"/statistical/score",
          component:Score  
        },{
          path:"/statistical/region",
          component:Region  
        },{
          path:"/operation/superLog",
          component:SuperLog
        },{
          path:"/operation/supplement",
          component:Supplement
        },{
          path:"/activityMoney/log",
          component:Money_log
        },{
          path:"/activityMoney/free",
          component:Money_free
        },{
          path:"/statistical/money",
          component:stat_money
        },{
          path:"/statistical/performance",
          component:Performance
        },{
          path:"/inventory/depot",
          component:Depot   
        },{
          path:"/inventory/goodsCheck",   
          component:GoodsCheck
        },{
          path:"/inventory/damage",
          component:Damage
        },{
          path:"/inventory/storeIn",
          component:StoreIn
        },{
          path:"/inventory/storeOut",
          component:StoreOut     
        },{
          path:"/inventory/stock",
          component:Stock
        },{
          path:"/inventory/access",
          component:Access  
        },{
          path:"/inventory/lack",
          component:Lack
        },{
          path:"/inventory/sales",
          component:Sales
        },{
          path:"/purchase/installed",
          component:Installed
        },{
          path:"/purchase/weaning",
          component:Weaning
        },{
          path:"/purchase/inventory",
          component:Inventory
        },{
          path:"/factory/Shenzhen",
          component:Shenzhen
        }
      ]
    }
  ]
})



