
<template>
  <div class="installed mainboss">
    <div class="martop">
      <div class="topleft"></div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建撤机单</el-button>
      </div>
    </div>

    <el-table
      :data="invenlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="serialNo" align="center" label="单号"></el-table-column>
      <el-table-column prop="hotelName" align="center" label="酒店名称"></el-table-column>
      <el-table-column prop="operatorName" align="center" label="负责人"></el-table-column>
      <el-table-column prop="amount" align="center" label="数量"></el-table-column>
      <el-table-column align="center" label="机型">
        <template slot-scope="scope">{{scope.row.type | fitertype}}</template>
      </el-table-column>
      <el-table-column align="center" label="状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="userName" align="center" label="操作人"></el-table-column>
      <el-table-column prop="updateTime" align="center" label="操作时间"></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
    v-if="DialogVisible && disval==1"
    title="创建撤机单"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="50%"
      center
    >
      <el-form
        v-if="DialogVisible"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="单号">
          <el-input v-model="ruleForm.serialNo" placeholder="请输入单号" clearable></el-input>
        </el-form-item>
        <el-form-item label="酒店名称" prop="hotelId">
          <el-cascader
            :placeholder="ruleForm.hotelName?ruleForm.hotelName:'请选择酒店'"
            :show-all-levels="false"
            :options="options"
            filterable
            clearable
            v-if="DialogVisible"
            @active-item-change="handleItemChange"
            @change="handlechange"
            @focus="getarea"
          ></el-cascader>
        </el-form-item>

        <el-form-item label="机型" prop="type">
          <el-select v-model="ruleForm.type" placeholder="请选择机型" clearable>
            <el-option v-for="item in types" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="负责人" prop="operatorId">
          <el-select v-model="ruleForm.operatorId" placeholder="请选择负责人" clearable @focus="getoperator">
            <el-option
              v-for="item in operatorlist"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="数量" prop="amount">
          <el-input v-model="ruleForm.amount" placeholder="请输入数量" clearable></el-input>
        </el-form-item>
        <el-form-item label="撤机理由" prop="remark">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入撤机理由"
            v-model="ruleForm.remark"
            maxlength="100"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

        <el-dialog
        v-if="DialogVisible && disval==2"
    title="编辑撤机单"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="50%"
      center
    >
      <el-form
        v-if="DialogVisible"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="单号">
          <el-input v-model="ruleForm.serialNo" placeholder="请输入单号" clearable></el-input>
        </el-form-item>
        <el-form-item label="酒店名称" prop="hotelId">
          <el-cascader
            :placeholder="ruleForm.hotelName?ruleForm.hotelName:'请选择酒店'"
            :show-all-levels="false"
            :options="options"
            filterable
            clearable
            v-if="DialogVisible"
            @active-item-change="handleItemChange"
            @change="handlechange"
            @focus="getarea"
          ></el-cascader>
        </el-form-item>

        <el-form-item label="机型" prop="type">
          <el-select v-model="ruleForm.type" placeholder="请选择机型" clearable>
            <el-option v-for="item in types" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="负责人" prop="operatorId">
          <el-select v-model="ruleForm.operatorId" placeholder="请选择负责人" clearable @focus="getoperator">
            <el-option
              v-for="item in operatorlist"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="数量" prop="amount">
          <el-input v-model="ruleForm.amount" placeholder="请输入数量" clearable></el-input>
        </el-form-item>
        <el-form-item label="撤机理由" prop="remark">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入撤机理由"
            v-model="ruleForm.remark"
            maxlength="100"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
var cheackNumber = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入金额"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("金额不应该小于0"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          value += " ";
          if (value.indexOf(".") != -1) {
            let arr = value.split(".");
            if (arr[1].length > 3) {
              callback(new Error("金额最多有两位小数"));
            } else {
              callback();
            }
          }
        }
      }
    } else {
      callback(new Error("金额应该是一个大于数的数字"));
    }
  }, 100);
};
var cheackNumber2 = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入一个数字"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("请输入一个大于0的数字"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          callback(new Error("请输入一个正整数"));
        }
      }
    } else {
      callback(new Error("请输入一个数字"));
    }
  }, 100);
};
export default {
  name: "installed",
  data() {
    return {
      inds: "",
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      DialogVisible: false,
      ruleForm: {
        sku: "",
        amount: "",
        hotelId: "",
        id: "",
        operatorId: "",
        remark: "",
        serialNo: "",
        status: "",
        type: "",
        updateTime: "",
        userId: ""
      },
      types: [
        {
          id: 1,
          label: "一代机"
        },
        {
          id: 2,
          label: "二代机"
        },
        {
          id: 3,
          label: "三代机"
        }
      ],
      rules: {
        serialNo: [
          { required: true, message: "请输入采购单号", trigger: "blur" }
        ],
        remark: [
          { required: true, message: "请输入撤机理由", trigger: "blur" }
        ],
        type: [{ required: true, message: "请选择机型", trigger: "change" }],
        operatorId: [
          { required: true, message: "请选择负责人", trigger: "change" }
        ],
        hotelId: [{ required: true, message: "请选择酒店", trigger: "change" }],
        amount: [{ required: true, validator: cheackNumber2, trigger: "blur" }]
      },
      options: [],
      goodsid: [],
      invenlist: [],
      moregoods: [],
      operatorlist: []
    };
  },
  watch: {
    DialogVisible: function() {
      if (!this.DialogVisible) {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
      } 
    }
  },
  filters: {
    fitertype: function(val) {
      if (val == 1) {
        return "一代机";
      } else if (val == 2) {
        return "二代机";
      } else if (val == 3) {
        return "三代机";
      }
    }
  },
  methods: {
    //查询区域数据
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("deviceOut/area").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.options = _data;
        });
      }
    },
    handleItemChange(val) {
      this.gethotel(val[0]);
    },
    //查询酒店下拉列表
    gethotel(areaId) {
      this.$http.get("deviceOut/hotel?areaId=" + areaId).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (areaId == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    handlechange(val) {
      this.ruleForm.hotelId = val[1];
    },
    //查询负责人列表
    getoperator() {
      if (this.operatorlist.length < 1) {
        this.$http.get("deviceOut/operatorList").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.id = res.data[i].id;
            _ob.label = res.data[i].name;
            _data.push(_ob);
          }
          this.operatorlist = _data;
        });
      }
    },
    //查询列表数据
    getlist() {
      let _Url =
        "deviceOut?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      this.$http.get(_Url).then(res => {
        let _data = res.data.data;
        for (let i in _data) {
          if (_data[i].status == 0) {
            _data[i].status = false;
          } else if (_data[i].status == 1) {
            _data[i].status = true;
          }
        }
        this.invenlist = _data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getlist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getlist();
    },
    //编辑
    handleedit(obj) {
      this.DialogVisible = true;
      this.disval = 2;
      let _Url = "deviceOut/" + obj.id;
      this.$http.get(_Url).then(res => {
        if (res.status == 200) {
          this.ruleForm.id = obj.id;
          for (let key in res.data) {
            if (res.data[key]) {
              this.ruleForm[key] = res.data[key];
            }
          }
        }
      });
    },
    //修改状态
    changestatus(obj) {
      let _Url = "deviceOut/status",
        _parms = {};
      obj.status = obj.status ? 1 : 0;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getlist();
        }
      });
    },
    //打开弹框
    openDialog(val) {
      this.disval = val;
      this.DialogVisible = true;
    },

    //点击确认按钮，进行表单校验
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("success", this.ruleForm);
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //保存数据
    savedata() {
      let _Url = "deviceOut",
        _parms = {},
        _Pur = [];

      this.ruleForm.userId = this.$store.state.userInfo.id;
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (_parms.id) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.DialogVisible = false;
            this.getlist();
          }
        });
      } else {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.DialogVisible = false;
            this.getlist();
          }
        });
      }
    },
    //重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  created() {
    this.getlist();
  }
};
</script>

<style lang="less">
.installed {
  fieldset {
    padding: 5px;
    border: 1px solid pink;
    margin-bottom: 20px;
    position: relative;
    .addbut {
      position: relative;
      left: 93.2%;
      margin-bottom: 10px;
    }
  }
}
</style>





