
<template>
  <div class="sales mainboss">
    <div class="martop">
      <div class="topleft">
        <el-select v-model="term.cityId" clearable filterable placeholder="请选择城市">
          <el-option v-for="item in citys" :key="item.value" :label="item.label" :value="item.id"></el-option>
        </el-select>
        <el-select
          v-model="term.areaId"
          clearable
          filterable
          placeholder="请选择区域"
          @focus="getarea"
          v-if="userinfo.type =='1'"
        >
          <el-option v-for="item in arealist" :key="item.value" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <el-input
          v-model="term.hotelName"
          v-if="userinfo.type =='1'"
          placeholder="请输入酒店名称"
          @keyup.enter.native="getsales(1)"
          clearable
        ></el-input>
        <el-select
          v-model="term.payway"
          clearable
          filterable
          @blur="searchBlur"
          placeholder="请选择支付方式"
        >
          <el-option
            v-for="item in paywaylist"
            :key="item.value"
            :label="item.label"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-date-picker
          v-model="term.startTime"
          type="datetime"
          placeholder="开始时间"
          align="right"
          :class="userinfo.type =='2'?'jd-input':''"
        ></el-date-picker>
        <el-date-picker
          v-model="term.endTime"
          type="datetime"
          placeholder="结束时间"
          align="right"
          :class="userinfo.type =='2'?'jd-input':''"
        ></el-date-picker>
        <el-button type="primary" @click="getsales(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="saleslist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="name" align="center" label="商品名称"></el-table-column>
      <!-- <el-table-column prop="quantity" align="center" label="销售量(个/件)"></el-table-column> -->
      <el-table-column prop="quantity" align="center" label="销售数量"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "sales",
  data() {
    return {
      userinfo: {},
      total: 0,
      ptotal: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      term: {
        cityId: "",
        areaId: "",
        hotelName: "",
        startTime: "",
        payway: "",
        endTime: ""
      },
      options: [],
      arealist: [],
      saleslist: [],
      citys: [
        {
          id: 1,
          label: "武汉"
        },
        {
          id: 2,
          label: "长沙"
        }
      ],
      paywaylist: [
        {
          id: 3,
          label: "微信支付"
        },
        {
          id: 2,
          label: "支付宝支付"
        }
      ]
    };
  },
  methods: {
    //查询区域下拉列表
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("goodsQuantity/areaList").then(res => {
          let _data = [];
          this.arealist = res.data;
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.options = _data;
        });
      }
    },
    handleItemChange(val) {
      this.gethotel(val[0]);
    },
    //查询酒店下拉列表
    gethotel(areaId) {
      this.$http.get("sales/hotelList/" + areaId).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (areaId == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    handlechange(val) {
      this.term.hotelId = val[1];
    },
    //隐藏支付方式选项
    searchBlur(e) {
      this.term.payway = e.target.value;
    },
    //获取列表数据
    getsales(val) {
      let _parms = {},
        _Url = "",
        _this = this;
      if (val == 1) {
        this.pageNum = 1;
      }
      if (this.term.startTime && this.term.endTime) {
        let _startTime = Number(this.term.startTime.getTime());
        let _endTime = Number(this.term.endTime.getTime());
        if (_startTime > _endTime) {
          this.term.endTime = "";
          this.$message.error("开始时间不得大于结束时间");
          return;
        } else if (_endTime - _startTime > 86400000 * 90) {
          this.term.endTime = "";
          this.$message.error("开始时间与结束时间间隔不得大于90天");
          return;
        }
      } else {
        this.$message("请选择时间");
        return;
      }
      _parms = { ...this.term };
      if (this.term.startTime) {
        _parms.startTime = this.Until.changDage(this.term.startTime);
      }
      if (this.term.endTime) {
        _parms.endTime = this.Until.changDage(this.term.endTime);
      }
      _Url = "goodsQuantity?";
      for (let key in _parms) {
        if (_parms[key]) {
          _Url += "&" + key + "=" + _parms[key];
        }
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        if (res.status == 200) {
          if (res.data.length > 0) {
            this.total = res.total;
            if (res.data.total < 1) {
              this.$message("没有查询到相关数据信息");
              this.saleslist = [];
            } else {
              res.data[0].name = "本页合计";
              setTimeout(() => {
                _this.saleslist = res.data;
              }, 200);
            }
          } else {
            this.$message("没有查询到相关数据信息");
            this.saleslist = [];
          }
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getsales();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getsales();
    }
  },
  created() {
    this.userinfo = this.$store.state.userInfo;
    const startTime = new Date();
    let _sttime = this.Until.changDage(new Date());
    let _date = _sttime.split(" ")[0];
    let _date2 = _date.split("-");
    let _date3 = _date2[0] + "-" + _date2[1] + "-1 00:00:00";
    startTime.setTime(new Date(_date3).getTime());
    this.term.startTime = startTime;

    const endTime = new Date();
    endTime.setTime(endTime.getTime());
    this.term.endTime = endTime;
    this.getsales();
  }
};
</script>
<style lang="less">
.sales {
  .topleft {
    .el-input {
      width: 20%;
    }
    .jd-input {
      width: 40%;
    }
  }
}
</style>



