<template>
  <div class="inventory mainboss">
    <div class="martop">
      <div class="topleft"></div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建采购单</el-button>
      </div>
    </div>

    <el-table
      :data="invenlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="serialNo" align="center" label="采购单号"></el-table-column>
      <el-table-column prop="fare" align="center" label="运费"></el-table-column>
      <el-table-column prop="totalAmount" align="center" label="总金额"></el-table-column>
      <el-table-column align="center" label="状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="userName" align="center" label="操作员"></el-table-column>
      <el-table-column prop="updateTime" align="center" label="更新时间"></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="DialogVisible && disval==1"
      title="创建采购单"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="70%"
      center
    >
      <el-form
        v-if="DialogVisible"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <fieldset>
          <legend>订单信息</legend>
          <el-form-item label="单号" required>
            <el-col :span="6">
              <el-form-item prop="serialNo">
                <el-input v-model="ruleForm.serialNo" placeholder="请输入采购单号" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item label="运费" prop="fare">
                <el-input
                  v-model="ruleForm.fare"
                  placeholder="请输入运费"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="采购金额" prop="totalAmount">
                <el-input
                  v-model="ruleForm.totalAmount"
                  placeholder="请输入采购金额"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="备注">
            <el-input
              v-model="ruleForm.remark"
              type="textarea"
              :rows="2"
              placeholder="请输入备注"
              maxlength="500"
              clearable
            ></el-input>
          </el-form-item>
        </fieldset>
        <fieldset>
          <legend>商品详情信息</legend>
          <el-button @click="addpurdetails" type="primary" icon="el-icon-plus" class="addbut"></el-button>
          <el-form-item
            label="商品编号"
            v-show="ruleForm.detaileList.length>0"
            v-for="(item,index) in ruleForm.detaileList"
            :key="index"
          >
            <el-col :span="4">
              <el-form-item>
                <el-input
                  v-model="item.sku"
                  placeholder="请输入商品编号"
                  clearable
                  @blur="getgoods(item.sku,1,index)"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="选择商品" :rules="rules.goodsid">
                <el-cascader
                  :placeholder="item.goodsName?item.goodsName:'请选择商品'"
                  :show-all-levels="false"
                  v-model="item.goodsid"
                  :options="moregoods"
                  filterable
                  clearable
                  @focus="getgoodsType(index)"
                  @active-item-change="handleItemChange"
                  @change="handlechange"
                ></el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="数量" :rules="rules.amount" :prop="'detaileList.' + index +'.amount'">
                <el-input
                  v-model="item.amount"
                  placeholder="请输入数量"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="单价" :rules="rules.unitPrice" :prop="'detaileList.' + index +'.unitPrice'">
                <el-input
                  v-model="item.unitPrice"
                  placeholder="请输入单价"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="0.5">--</el-col>
            <el-col :span="1">
              <el-button @click="lesspurdetails(index)" type="primary" icon="el-icon-minus"></el-button>
            </el-col>
          </el-form-item>
        </fieldset>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
    v-if="DialogVisible && disval==2"
      title="编辑采购单"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="70%"
      center
    >
      <el-form
        v-if="DialogVisible"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <fieldset>
          <legend>订单信息</legend>
          <el-form-item label="单号" required>
            <el-col :span="6">
              <el-form-item prop="serialNo">
                <el-input v-model="ruleForm.serialNo" placeholder="请输入采购单号" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item label="运费" prop="fare">
                <el-input
                  v-model="ruleForm.fare"
                  placeholder="请输入运费"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="采购金额" prop="totalAmount">
                <el-input
                  v-model="ruleForm.totalAmount"
                  placeholder="请输入采购金额"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="备注">
            <el-input
              v-model="ruleForm.remark"
              type="textarea"
              :rows="2"
              placeholder="请输入备注"
              maxlength="500"
              clearable
            ></el-input>
          </el-form-item>
        </fieldset>
        <fieldset>
          <legend>商品详情信息</legend>
          <el-button @click="addpurdetails" type="primary" icon="el-icon-plus" class="addbut"></el-button>
          <el-form-item
            label="商品编号"
            v-show="ruleForm.detaileList.length>0"
            v-for="(item,index) in ruleForm.detaileList"
            :key="index"
          >
            <el-col :span="4">
              <el-form-item>
                <el-input
                  v-model="item.sku"
                  placeholder="请输入商品编号"
                  clearable
                  @blur="getgoods(item.sku,1,index)"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="选择商品" :rules="rules.goodsid">
                <el-cascader
                  :placeholder="item.goodsName?item.goodsName:'请选择商品'"
                  :show-all-levels="false"
                  v-model="item.goodsid"
                  :options="moregoods"
                  filterable
                  clearable
                  @focus="getgoodsType(index)"
                  @active-item-change="handleItemChange"
                  @change="handlechange"
                ></el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="数量" :rules="rules.amount" :prop="'detaileList.' + index +'.amount'">
                <el-input
                  v-model="item.amount"
                  placeholder="请输入数量"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="单价" :rules="rules.unitPrice" :prop="'detaileList.' + index +'.unitPrice'">
                <el-input
                  v-model="item.unitPrice"
                  placeholder="请输入单价"
                  maxlength="256"
                  clearable
                  type="number"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="0.5">--</el-col>
            <el-col :span="1">
              <el-button @click="lesspurdetails(index)" type="primary" icon="el-icon-minus"></el-button>
            </el-col>
          </el-form-item>
        </fieldset>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
var cheackNumber = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入金额"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("金额不应该小于0"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          value += " ";
          if (value.indexOf(".") != -1) {
            let arr = value.split(".");
            if (arr[1].length > 3) {
              callback(new Error("金额最多有两位小数"));
            } else {
              callback();
            }
          }
        }
      }
    } else {
      callback(new Error("金额应该是一个大于数的数字"));
    }
  }, 100);
};
var cheackNumber2 = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入一个数字"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("请输入一个大于0的数字"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          callback(new Error("请输入一个正整数"));
        }
      }
    } else {
      callback(new Error("请输入一个数字"));
    }
  }, 100);
};
export default {
  name: "inventory",
  data() {
    return {
      inds: "",
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      DialogVisible: false,
      ruleForm: {
        serialNo: "",
        fare: "",
        totalAmount: "",
        remark: "",
        detaileList: []
      },
      rules: {
        serialNo: [
          { required: true, message: "请输入采购单号", trigger: "blur" }
        ],
        fare: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        totalAmount: [
          { required: true, validator: cheackNumber, trigger: "blur" }
        ],
        goodsid: [
          {
            required: true,
            message: "请选择商品",
            trigger: ["blur", "change"]
          }
        ],
        amount: [{ required: true, validator: cheackNumber2, trigger: "blur" }],
        unitPrice: [
          { required: true, validator: cheackNumber, trigger: "blur" }
        ]
      },
      goodsid: [],
      invenlist: [],
      moregoods: []
    };
  },
  watch: {
    DialogVisible: function() {
      if (!this.DialogVisible) {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
        this.ruleForm.detaileList = [];
      }
    }
  },
  methods: {
    getlist() {
      let _Url =
        "purchase?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      this.$http.get(_Url).then(res => {
        let _data = [...res.data.data];
        for (let i in _data) {
          if (_data[i].status == 0) {
            _data[i].status = false;
          } else if (_data[i].status == 1) {
            _data[i].status = true;
          }
          _data[i].updateTime='20'+_data[i].updateTime;
        }
        this.invenlist = _data;
        this.total = res.data.total;
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getlist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getlist();
    },
    //编辑
    handleedit(obj) {
      this.DialogVisible = true;
      this.disval = 2;
      let _Url = "purchase/" + obj.id;
      this.$http.get(_Url).then(res => {
        if (res.status == 200) {
          for (let key in res.data) {
            if (res.data[key]) {
              this.ruleForm[key] = res.data[key];
            }
          }
        }
      });
    },
    //修改状态
    changestatus(obj) {
      let _Url = "purchase/status",
        _parms = {};
      obj.status = obj.status ? 1 : 0;
      _parms = {
        status: obj.status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getlist();
        }
      });
    },
    //打开弹框
    openDialog(val) {
      this.disval = val;
      if (this.ruleForm.detaileList && this.ruleForm.detaileList.length == 0) {
        let obj = {
          sku: "",
          goodsId: "",
          amount: "",
          goodsid: [],
          unitPrice: ""
        };
        this.ruleForm.detaileList.push(obj);
      }
      this.DialogVisible = true;
    },
    //查询商品类别
    getgoodsType(index) {
      this.inds = index ? index : 0;
      let _Url = "purchase/goodsType";
      if (this.moregoods.length < 1) {
        this.$http.get(_Url).then(res => {
          for (let i in res.data) {
            res.data[i].value = res.data[i].id;
            res.data[i].label = res.data[i].name;
            res.data[i].children = [];
          }
          this.moregoods = res.data;
        });
      }
    },
    handlechange(val) {
      let _this = this;
      let index = this.inds;
      setTimeout(() => {
        let _data = [];
        if (val[1]) {
          _this.ruleForm.detaileList[_this.inds].goodsId = val[1];
          _data = val;
        } else {
          _this.ruleForm.detaileList[_this.inds].goodsId = val[0];
          _data = val;
        }
        _this.ruleForm.detaileList[_this.inds].goodsid = _data;
      }, 500);
    },
    handleItemChange(val) {
      this.getgoods(val[0], 2);
    },
    //查询商品
    getgoods(val, ind, index) {
      let _Url = "purchase/goods";
      this.inds = index;
      if (ind == 1) {
        _Url += "?sku=" + val;
      } else if (ind == 2) {
        _Url += "?typeId=" + val;
      }
      this.$http.get(_Url).then(res => {
        if (ind == 1) {
          let _data = [],
            isun = false;
          _data.push(res.data.id);
          res.data.label = res.data.name;
          res.data.value = res.data.id;

          for (let i in this.moregoods) {
            if (this.moregoods[i].id == res.data.id) {
              isun = false;
            } else {
              isun = true;
            }
          }
          if (isun) {
            this.moregoods.push(res.data);
          }

          this.ruleForm.detaileList[index].goodsId = res.data.id;
          this.ruleForm.detaileList[index].goodsid = _data;
        } else if (ind == 2) {
          let _datas = [];
          for (let i in this.moregoods) {
            if (val == this.moregoods[i].id) {
              for (let j in res.data) {
                let _obj = {};
                _obj.value = res.data[j].id;
                _obj.label = res.data[j].name;
                _datas.push(_obj);
              }
              _datas = this.unique(_datas);
              this.moregoods[i].children = _datas;
            }
          }
        }
      });
    },
    //数组去重
    unique(arr) {
      return Array.from(new Set(arr));
    },
    //添加商品
    addpurdetails() {
      let obj = {
        sku: "",
        goodsId: "",
        amount: "",
        goodsid: [],
        unitPrice: ""
      };
      this.ruleForm.detaileList.push(obj);
    },
    //删除某件商品
    lesspurdetails(val) {
      let _pur = this.ruleForm.detaileList;
      for (let i in _pur) {
        if (val == i) {
          _pur.splice(i, 1);
        }
      }
    },
    //点击确认按钮，进行表单校验
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("success", this.ruleForm);
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //保存数据
    savedata() {
      let _Url = "purchase",
        _parms = {},
        _Pur = [],
        _purdetails = this.ruleForm.detaileList;
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      for (let i in _purdetails) {
        if (_purdetails[i].amount) {
          let _obj = {};
          _obj.amount = _purdetails[i].amount;
          _obj.unitPrice = _purdetails[i].unitPrice;
          _obj.goodsId = _purdetails[i].goodsId;
          _Pur.push(_obj);
        }
      }
      _parms.detaileList = _Pur;
      if (_Pur.length == 0) {
        this.$message("请输入商品详情信息");
      } else {
        if (_parms.id) {
          this.$http.put(_Url, _parms).then(res => {
            if (res.status == 200) {
              this.$message.success("保存成功");
              this.DialogVisible = false;
              this.getlist();
            }
          });
        } else {
          this.$http.post(_Url, _parms).then(res => {
            if (res.status == 200) {
              this.$message.success("保存成功");
              this.DialogVisible = false;
              this.getlist();
            }
          });
        }
      }
    },
    //重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  created() {
    this.getlist();
  }
};
</script>

<style lang="less">
.inventory {
  fieldset {
    padding: 5px;
    border: 1px solid pink;
    margin-bottom: 20px;
    position: relative;
    .addbut {
      position: relative;
      left: 93.2%;
      margin-bottom: 10px;
    }
  }
}
</style>





