
<template>
  <div class="score mainboss">
    <div class="martop">
      <div class="topleft">
        <el-date-picker v-model="term.startTime" type="datetime" placeholder="开始时间" align="right"></el-date-picker>
        <el-date-picker v-model="term.endTime" type="datetime" placeholder="结束时间" align="right"></el-date-picker>
        <el-button type="primary" @click="getsales(1)">搜索</el-button>
      </div>
      <div class="topright">
        <el-button type="primary" @click="openDialog">评价标准</el-button>
      </div>
    </div>
    <el-table
      :data="scorelist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      :row-class-name="tableRowClassName"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="name" align="center" label="区域"></el-table-column>
      <el-table-column prop="quantity" align="center" label="补货数量"></el-table-column>
      <el-table-column prop="score" align="center" label="得分"></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button @click="handlebut(scope.row)" type="primary" size="mini" icon="el-icon-menu">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="考核详情"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      :height="this.$store.state.tableHeight"
      width="50%"
      center
    >
      <el-table
        :data="detailslist"
        border
        style="width: 100%"
        v-loading="loading"
        height="500"
        :row-class-name="tableRowClassName"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        element-loading-text="数据正在加载中"
      >
        <el-table-column prop="name" align="center" label="酒店"></el-table-column>
        <el-table-column prop="quantity" align="center" label="补货数量"></el-table-column>
        <el-table-column prop="score" align="center" label="得分"></el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog
      title="考核标准"
      :visible.sync="DialogVisible"
      :modal-append-to-body="false"
      :height="this.$store.state.tableHeight"
      width="50%"
      center
    >
      <el-form ref="ruleForm" :rules="rules" :model="score" label-width="140px">
        <el-form-item label="24小时以内" prop="less24">
          <el-input type="number" v-model.number="score.less24"></el-input>
        </el-form-item>
        <el-form-item label="48小时以内" prop="less48">
          <el-input type="number" v-model.number="score.less48"></el-input>
        </el-form-item>
        <el-form-item label="72小时以内" prop="less72">
          <el-input type="number" v-model.number="score.less72"></el-input>
        </el-form-item>
        <el-form-item label="96小时以内" prop="less96">
          <el-input type="number" v-model.number="score.less96"></el-input>
        </el-form-item>
        <el-form-item label="120小时以内" prop="less120">
          <el-input type="number" v-model.number="score.less120"></el-input>
        </el-form-item>
        <el-form-item label="120小时以上" prop="more120">
          <el-input type="number" v-model.number="score.more120"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">提交修改</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
var cheackNumber = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入评分"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (Number.isInteger(value)) {
        callback();
      } else {
        callback(new Error("评分应该是一个整数"));
      }
    } else {
      callback(new Error("请输入评分"));
    }
  }, 100);
};
export default {
  name: "score",
  data() {
    return {
      loading: false,
      DialogVisible: false,
      centerDialogVisible: false,
      term: {
        startTime: "",
        endTime: ""
      },
      newterm: {},
      detailslist: [],
      scorelist: [],
      score: {
        less24: "",
        less48: "",
        less72: "",
        less96: "",
        less120: "",
        more120: ""
      },
      rules: {
        less24: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        less48: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        less72: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        less96: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        less120: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        more120: [{ required: true, validator: cheackNumber, trigger: "blur" }]
      }
    };
  },
  methods: {
    //隔行变色
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "gray";
      }
    },
    //获取列表数据
    getsales(val) {
      let _parms = {},
        _Url = "",
        _this = this;
      if (val == 1) {
        this.pageNum = 1;
      }
      if (this.term.startTime && this.term.endTime) {
        let _startTime = Number(this.term.startTime.getTime());
        let _endTime = Number(this.term.endTime.getTime());
        if (_startTime > _endTime) {
          this.term.endTime = "";
          this.$message.error("开始时间不得大于结束时间");
          return;
        } else if (_endTime - _startTime > 86400000 * 90) {
          this.term.endTime = "";
          this.$message.error("开始时间与结束时间间隔不得大于90天");
          return;
        }
      } else {
        this.$message("请选择时间");
        return;
      }

      if (this.term.startTime) {
        _parms.startTime = this.Until.changDage(this.term.startTime);
      }
      if (this.term.endTime) {
        _parms.endTime = this.Until.changDage(this.term.endTime);
      }
      this.newterm = _parms;
      _Url = "OperationScore?";
      let _par = "";
      for (let key in _parms) {
        if (_parms[key]) {
          _Url += "&" + key + "=" + _parms[key];
        }
      }
      _par = _par.substr(1);
      _Url += _par;

      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        if (res.status == 200) {
          // res.data = res.data.sort(this.Until.compare("score",2));
          this.scorelist = res.data;
        }
      });
    },
    //点击详情
    handlebut(obj) {
      let _Url = "OperationScore/hotel?";
      this.newterm.id = obj.id;
      let _par = "";
      for (let key in this.newterm) {
        if (this.newterm[key]) {
          _Url += "&" + key + "=" + this.newterm[key];
        }
      }
      _par = _par.substr(1);
      _Url += _par;
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        if (res.status == 200) {
          this.centerDialogVisible = true;
          // res.data =  res.data.sort(this.Until.compare("score",2));
          this.detailslist = res.data;
        }
      });
    },
    //点击评分标准
    openDialog() {
      this.DialogVisible = !this.DialogVisible;
      this.getscore();
    },
    //获取评分标准
    getscore() {
      this.$http("OperationScore/get").then(res => {
        this.score = { ...res.data };
        console.log(this.score);
      });
    },
    //提交修改数据
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let _Url = "OperationScore",
            _parms = { ...this.score };
          this.$http.put(_Url, _parms).then(res => {
            if (res.status == 200) {
              this.$message({
                message: "操作成功！",
                type: "success"
              });
              this.DialogVisible = !this.DialogVisible;
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
      console.log("submit!");
    }
  },
  created() {
    const startTime = new Date();
    startTime.setTime(startTime.getTime() - 3600 * 1000 * 24 * 7 + 60);
    this.term.startTime = startTime;

    const endTime = new Date();
    endTime.setTime(endTime.getTime());
    this.term.endTime = endTime;
    this.getsales();
  }
};
</script>
<style lang="less">
.score {
  .topleft {
    .el-input {
      width: 40%;
    }
  }
}
</style>



