<template>
  <div class="stock mainboss">
    <div class="martop">
      <div class="topleft">
        <el-select v-model="term.cityId" clearable filterable placeholder="请选择城市">
          <el-option v-for="item in citys" :key="item.value" :label="item.label" :value="item.id"></el-option>
        </el-select>
        <el-input
          v-model="term.sku"
          placeholder="请输入商品编码"
          @keyup.enter.native="getgoodslist(1)"
          clearable
        ></el-input>
        <el-input
          v-model="term.name"
          placeholder="请输入商品名称"
          @keyup.enter.native="getgoodslist(1)"
          clearable
        ></el-input>
        <el-select v-model="term.typeId" clearable filterable placeholder="请选择类别" @focus="gettype">
          <el-option v-for="item in types" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <el-button type="primary" @click="getsales(1)">搜索</el-button>
      </div>
    </div>
    <el-table
      :data="saleslist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      stripe
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="name" align="center" label="酒品名称"></el-table-column>
      <el-table-column prop="typeName" align="center" label="种类"></el-table-column>
      <el-table-column prop="amount" align="center" label="库存"></el-table-column>
      <!-- <el-table-column prop="hotelAmount" align="center" label="酒店库存"></el-table-column>
      <el-table-column prop="totalAmount" align="center" label="总库存"></el-table-column>
      <el-table-column prop="costPrice" align="center" label="成本价"></el-table-column>
      <el-table-column prop="totalCost" align="center" label="总成本"></el-table-column> -->
    </el-table>
  </div>
</template>
<script>
export default {
  name: "stock",
  data() {
    return {
      total: 0,
      ptotal: 0,
      pageNum: 1,
      pageSize: 10,
      loading: false,
      term: {
        name: "",
        sku: "",
        cityId:"",
        typeId: ""
      },
      citys: [
        {
          id: 1,
          label: "武汉"
        },
        {
          id: 2,
          label: "长沙"
        }
      ],
      types: [],
      arealist: [],
      saleslist: []
    };
  },
  methods: {
    //查询类别数据
    gettype() {
      if (this.types.length < 1) {
        this.$http.get("storeCount/type").then(res => {
          if (res.status == 200) {
            this.types = res.data;
          }
        });
      }
    },
    //获取列表数据
    getsales(val) {
      let _parms = {},
        _Url = "storeCount?",
        _this = this;
      if (val == 1) {
        this.pageNum = 1;
      }
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += key + "=" + this.term[key]+"&" ;
        }
      }
      _Url = _Url.substr(0,_Url.length-1);
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.total = res.total;
        if (res.data.total < 1) {
          this.$message("没有查询到相关数据信息");
          this.saleslist = [];
        } else {
          _this.saleslist = res.data.data;
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getsales();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getsales();
    }
  },
  created() {
    this.getsales();
  }
};
</script>
<style lang="less">
.stock {
  .topleft {
    .el-input {
      width: 20%;
    }
  }
}
</style>



