<template>
  <div class="store_list mainboss">
    <div class="martop">
      <div class="topleft">
        <!-- <el-input
          v-model="term.goodsSku"
          placeholder="请输入商品编码"
          @keyup.enter.native="getstorelist(1)"
          clearable
        ></el-input>
        <el-input
          v-model="term.hotelSku"
          placeholder="请输入酒店编码"
          @keyup.enter.native="getstorelist(1)"
          clearable
        ></el-input> -->
        <el-input
          v-model="term.hotelName"
          placeholder="请输入酒店名称"
          @keyup.enter.native="getstorelist(1)"
          clearable
        ></el-input>
        <el-select v-model="term.areaId" clearable filterable placeholder="请选择区域" @focus="getarea">
          <el-option v-for="item in area" :key="item.value" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <el-cascader
          placeholder="请选择类别商品"
          :show-all-levels="false"
          :options="types"
          filterable
          clearable
          @active-item-change="handleItemChange1"
          @change="handlechange1"
          @focus="gettypes"
        ></el-cascader>

        <el-cascader
          placeholder="选择区域酒店"
          :show-all-levels="false"
          :options="options"
          filterable
          clearable
          @active-item-change="handleItemChange"
          @change="handlechange"
          @focus="getarea"
        ></el-cascader>

        <el-button type="primary" @click="getstorelist(1)">搜索</el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-download" @click="downloadTable">导出表格</el-button>
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建库存</el-button>
      </div>
    </div>

    <el-table
      id="mytable"
      :data="storelist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
    >
      <el-table-column prop="goodsName" align="center" label="商品名称"></el-table-column>
      <el-table-column prop="hotelName" align="center" label="酒店名称"></el-table-column>
      <!-- <el-table-column prop="total" align="center" label="商品总数"></el-table-column> -->
      <el-table-column prop="currentStock" align="center" label="当前库存"></el-table-column>
      <el-table-column prop="securityStock" align="center" label="安全库存"></el-table-column>
      <!-- <el-table-column prop="costPrice" align="center" label="成本价格"></el-table-column> -->
      <el-table-column prop="salePrice" align="center" label="销售价格"></el-table-column>
      <!-- <el-table-column prop="totalAmount" align="center" label="总金额"></el-table-column> -->
      <!-- <el-table-column align="center" label="当前状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column> -->
      <el-table-column prop="remark" align="center" label="备注"></el-table-column>
      <el-table-column label="操作" align="center" width="190">
        <template slot-scope="scope">
          <el-button
            :disabled="!scope.row.status"
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
          <el-button
            @click="handledel(scope.row)"
            type="danger"
            size="mini"
            icon="el-icon-delete"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="pageSize"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>
    
    <el-dialog
      :title="disval==1?'创建库存':'编辑库存'"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
    
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
        v-show="disval==1 || disval==2"
        v-if="centerDialogVisible"
      >
        <el-form-item label="酒店名称" prop="hotelId">
          <el-cascader
            @focus="getarea"
            :placeholder="actuserobj.hotelName"
            :show-all-levels="false"
            :options="options"
            filterable
            clearable
            @active-item-change="handleItemChange"
            @change="handlechange"
            :disabled="disval!=1?true:false"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="商品名称" prop="goodsId">
          <el-cascader
            :placeholder="actuserobj.goodsName?actuserobj.goodsName:'请选择类别商品'"
            :show-all-levels="false"
            :options="types"
            filterable
            clearable
            @active-item-change="handleItemChange1"
            @change="handlechange1"
            @focus="gettypes"
            :disabled="disval!=1?true:false"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="销售价格" prop="salePrice">
          <el-input
            v-model="ruleForm.salePrice"
            placeholder="请输入销售价格"
            type="number"
            oninput="if(value.length>10)value=value.slice(0,10)"
            clearable
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="商品总数">
          <el-input
            v-model="ruleForm.total"
            placeholder="请输入商品总数"
            type="number"
            oninput="if(value.length>11)value=value.slice(0,11)"
            clearable
          ></el-input>
        </el-form-item> -->
        <el-form-item label="当前库存" prop="currentStock">
          <el-input
            v-model="ruleForm.currentStock"
            placeholder="请输入当前库存"
            type="number"
            oninput="if(value.length>11)value=value.slice(0,11)"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="安全库存">
          <el-input
            v-model="ruleForm.securityStock"
            placeholder="请输入安全库存"
            type="number"
            oninput="if(value.length>11)value=value.slice(0,11)"
            clearable
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="状态" prop="status">
          <el-select v-model="ruleForm.status" placeholder="请选择状态">
            <el-option v-for="item in typelist" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="备注">
          <el-input
            v-model="ruleForm.remark"
            type="textarea"
            :rows="2"
            placeholder="请输入备注"
            oninput="if(value.length>500)value=value.slice(0,500)"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
var checkEmail = (rule, value, callback) => {
  const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
  if (!value) {
    return callback(new Error("邮箱不能为空"));
  }
  setTimeout(() => {
    if (mailReg.test(value)) {
      callback();
    } else {
      callback(new Error("请输入正确的邮箱格式"));
    }
  }, 100);
};
var cheackNumber = (rule, value, callback) => {
  value *= 1;
  // if(String(value)=='0'){
  //   callback();
  // }else 
  if (!value) {
    callback(new Error("请输入一个数字"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("请输入一个大于0的数字"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          callback(new Error("请输入一个正整数"));
        }
      }
    } else {
      callback(new Error("请输入一个数字"));
    }
  }, 100);
};
var cheackNumber2 = (rule, value, callback) => {
  value *= 1;
  if (!value) {
    callback(new Error("请输入销售价格"));
  }
  setTimeout(() => {
    if (Number.isFinite(value)) {
      if (value < 0) {
        callback(new Error("销售价格应该大于0"));
      } else {
        if (Number.isInteger(value)) {
          callback();
        } else {
          value += " ";
          if (value.indexOf(".") != -1) {
            let arr = value.split(".");
            if (arr[1].length > 3) {
              callback(new Error("价格最多有两位小数"));
            } else {
              callback();
            }
          }
        }
      }
    } else {
      callback(new Error("销售价格应该是一个大于0的数字"));
    }
  }, 100);
};

import FileSaver from "file-saver";
import XLSX from "xlsx";

export default {
  name: "store_list",
  data() {
    return {
      total: 0,
      ptotal: 0,
      disval: 1,
      pageNum: 1,
      pageSize: 10,
      username: "",
      isdown: false,
      loading: false,
      centerDialogVisible: false,
      term: {
        goodsId: "",
        hotelName: "",
        hotelId: "",
        goodsSku: "",
        goodsSku: "",
        typeId: "",
        areaId: ""
      },
      actuserobj: {},
      ruleForm: {
        currentStock: "",
        goodsId: "",
        hotelId: "",
        id: "",
        remark: "",
        salePrice: "",
        securityStock: "",
        status: "",
        total: ""
      },
      rules: {
        goodsId: [{ required: true, message: "请选择商品", trigger: "change" }],
        hotelId: [{ required: true, message: "请选择酒店", trigger: "change" }],
        salePrice: [
          { required: true, validator: cheackNumber2, trigger: "blur" }
        ],
        securityStock: [
          { required: true, validator: cheackNumber, trigger: "blur" }
        ],
        currentStock: [
          { required: true, validator: cheackNumber, trigger: "blur" }
        ],
        total: [{ required: true, validator: cheackNumber, trigger: "blur" }],
        email: [{ required: true, validator: checkEmail, trigger: "blur" }],
        status: [
          { required: true, message: "请选择用户类型", trigger: "change" }
        ]
      },
      downlist: [],
      rolelist: [],
      typelist: [
        {
          id: 1,
          label: "有效"
        },
        {
          id: 0,
          label: "无效"
        }
      ],
      area: [],
      types: [],
      options: [],
      hotellist: [],
      goodsList: [],
      storelist: []
    };
  },
  watch: {
    centerDialogVisible: function() {
      if (!this.centerDialogVisible) {
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
        this.actuserobj = {};
      }
    }
  },
  methods: {
    //查询区域下拉列表
    getarea() {
      if (this.options.length < 1) {
        this.$http.get("store/areaList").then(res => {
          let _data = [],
            _area = [];
          for (let i in res.data) {
            let _ob = {},
              _objarea = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _objarea.value = res.data[i].id;
            _objarea.id = res.data[i].id;
            _objarea.name = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
            _area.push(_objarea);
          }
          this.area = _area;
          this.options = _data;
        });
      }
    },
    handleItemChange(val) {
      this.gethotel(val[0]);
    },
    handleItemChange1(val) {
      this.getgoodsList(val[0]);
    },
    //查询酒店下拉列表
    gethotel(areaId) {
      this.$http.get("store/hotelList/" + areaId).then(res => {
        let _datas = [];
        for (let i in this.options) {
          if (areaId == this.options[i].value) {
            for (let j in res.data) {
              let _obj = {};
              _obj.value = res.data[j].id;
              _obj.label = res.data[j].name;
              _datas.push(_obj);
            }
            this.options[i].children = _datas;
          }
        }
      });
    },
    //查询类别
    gettypes() {
      if (this.types.length < 1) {
        this.$http.get("store/type").then(res => {
          let _data = [];
          for (let i in res.data) {
            let _ob = {};
            _ob.value = res.data[i].id;
            _ob.label = res.data[i].name;
            _ob.children = [];
            _data.push(_ob);
          }
          this.types = _data;
        });
      }
    },
    handlechange(val) {
      if (this.centerDialogVisible) {
        this.ruleForm.hotelId = val[1];
      } else {
        this.term.hotelId = val[1];
      }
    },
    handlechange1(val) {
      let _types = this.types,
        _obj = {};
      for (let i in _types) {
        if (_types[i].value == val[0]) {
          for (let j in _types[i].children) {
            if (_types[i].children[j].value == val[1]) {
              _obj = _types[i].children[j];
            }
          }
        }
      }
      if (this.centerDialogVisible) {
        this.ruleForm.goodsId = val[1];
        this.ruleForm.salePrice = _obj.salePrice;
      } else {
        this.term.goodsId = val[1];
      }
    },

    //选择商品
    handlechangesel(val) {
      this.ruleForm.goodsId = val;
      for (let i in this.goodsList) {
        if (val == this.goodsList[i].id) {
          this.ruleForm.salePrice = this.goodsList[i].salePrice;
        }
      }
    },
    //查询商品下拉列表
    getgoodsList(typeId) {
      if (this.goodsList.length < 1) {
        this.$http.get("store/goodsList?typeId=" + typeId).then(res => {
          let _datas = [];
          for (let i in this.types) {
            if (typeId == this.types[i].value) {
              for (let j in res.data) {
                let _obj = {};
                _obj.value = res.data[j].id;
                _obj.label = res.data[j].name;
                _obj.salePrice = res.data[j].salePrice;
                _datas.push(_obj);
              }
              this.types[i].children = _datas;
            }
          }
          // this.goodsList = res.data;
        });
      }
    },
    //获取列表数据
    getstorelist(val) {
      let _Url = "",
        _this = this;
      if (val == 1) {
        this.pageNum = 1;
        _Url = "store?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      }else if(val =="down"){
        _Url = "store?pageNum=" + this.pageNum + "&pageSize=1000";
      }else{
        _Url = "store?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize;
      }
      
      for (let key in this.term) {
        if (this.term[key]) {
          _Url += "&" + key + "=" + this.term[key];
        }
      }
      this.loading = true;
      this.$http.get(_Url).then(res => {
        
        if (res.data.total < 1) {
          this.loading = false;
          this.$message("没有查询到相关数据信息");
          this.storelist = [];
        } else {
          let _data = res.data.data;

          if (this.isdown) {
            let newData = [..._data];

            this.downlist = newData;
            this.isdown = !this.isdown;
            setTimeout(() => {
              _this.loading = false;
              _this.output();
            }, 150);
          } else {
            this.total = res.data.total;
            for (let i in _data) {
              if (_data[i].status == 1) {
                _data[i].status = true;
              } else {
                _data[i].status = false;
              }
            }
            this.storelist = _data;
            this.loading = false;
          }
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getstorelist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getstorelist();
    },
    //打开编辑弹框
    openDialog(val) {
      this.disval = val;
      this.centerDialogVisible = !this.centerDialogVisible;
    },
    //点击编辑某条数据
    handleedit(obj) {
      this.getidinfo(obj.id);
      this.openDialog(2);
      this.actuserobj = obj;
    },
    //删除某条数据
    handledel(obj) {
      console.log("obj:",obj)
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http.delete("store/" + obj.id).then(res => {
            console.log("res:",res)
            this.$message.success("修改成功");
            this.getstorelist();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //通过ID查询某个数据信息
    getidinfo(id) {
      this.$http.get("store/" + id).then(res => {
        let obj = res.data;
        this.actuserobj.children = obj;
        obj.remark = obj.remark ? obj.remark : "";
        // Number(obj.status)
        for (let key in obj) {
          // if (obj[key]) {
          this.ruleForm[key] = obj[key];
          // }
        }
      });
    },
    //修改用户状态
    changestatus(obj) {
      let _Url = "store/status",
        _parms = {
          status: "",
          id: obj.id
        };
      if (obj.status) {
        _parms.status = 1;
      } else {
        _parms.status = 2;
      }
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
        }
      });
    },
    //保存
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          return false;
        }
      });
    },
    //重置
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
        this.ruleForm.remark = "";
      }
    },
    //保存编辑
    savedata() {
      let _Url = "store",
        _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          console.log("1",res)
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getstorelist();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          console.log(this.disval,res)
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getstorelist();
          }
        });
      }
    },
    downloadTable() {
      this.isdown = true;
      this.getstorelist("down");
    },
    output() {
      // downlist
      let _downlist = this.downlist;
      var str =
        "<tr><td>商品名称</td><td>酒店名称</td><td>商品总数</td><td>当前库存</td><td>安全库存</td><td>成本价格</td><td>销售价格</td><td>总金额</td><td>当前状态</td><td>备注</td></tr>";
      //循环遍历，每行加入tr标签，每个单元格加td标签
      for (let i = 0; i < _downlist.length; i++) {
        str += "<tr>";
        str += `<td>${_downlist[i].goodsName + "\t"}</td>`;
        str += `<td>${_downlist[i].hotelName + "\t"}</td>`;
        str += `<td>${_downlist[i].total + "\t"}</td>`;
        str += `<td>${_downlist[i].currentStock + "\t"}</td>`;
        str += `<td>${_downlist[i].securityStock + "\t"}</td>`;
        str += `<td>${_downlist[i].costPrice?_downlist[i].costPrice:'' + "\t"}</td>`;
        str += `<td>${_downlist[i].salePrice?_downlist[i].salePrice:'' + "\t"}</td>`;
        str += `<td>${_downlist[i].totalAmount?_downlist[i].totalAmount:'' + "\t"}</td>`;
        str += `<td>${_downlist[i].status==1?'正常':'关闭' + "\t"}</td>`;
        str += `<td>${_downlist[i].remark?_downlist[i].remark:'' + "\t"}</td>`;
        str += "</tr>";
      }
      //Worksheet名
      var worksheet = "商品库存";
      var uri = "data:application/vnd.ms-excel;base64,";

      //下载的表格模板数据
      var template = `<html xmlns:o="urn:schemas-microsoft-com:office:office" 
      xmlns:x="urn:schemas-microsoft-com:office:excel" 
      xmlns="http://www.w3.org/TR/REC-html40">
      <head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet>
        <x:Name>${worksheet}</x:Name>
        <x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet>
        </x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]-->
        </head><body><table>${str}</table></body></html>`;
      //下载模板
      window.location.href = uri + this.base64(template);
    },
    base64(s) {
      return window.btoa(unescape(encodeURIComponent(s)));
    },
    output2() {
      /* generate workbook object from table */
      let table = document.querySelector("#mytable").cloneNode(true); // 判断要导出的节点中是否有fixed的表格，如果有，转换excel时先将该dom移除，然后append回去，
      /* generate workbook object from table */
      var fix = document.querySelector(".el-table__fixed");
      var wb;
      if (fix) {
        wb = XLSX.utils.table_to_book(
          document.querySelector("#mytable").removeChild(fix)
        );
        document.querySelector("#mytable").appendChild(fix);
      } else {
        wb = XLSX.utils.table_to_book(document.querySelector("#mytable"));
      }

      let excName = "商品库存";
      if (this.dataEndDate) {
        excName += "(" + this.dataEndDate + ")";
      }
      let wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          excName + ".xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    }
  },
  created() {
    this.getstorelist();
  }
};
</script>

<style lang="less">
.store_list {
  .topleft {
    .el-input {
      width: 20%;
    }
    .el-select {
      width: 20%;
    }
  }
}
</style>





