
<template>
  <div class="operation_permission mainboss">
    <div class="martop">
      <div class="topleft"></div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="openDialog(1)">创建权限</el-button>
      </div>
    </div>

    <el-table
      :data="permissionlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text="数据正在加载中"
      style="width: 100%"
    >
      <el-table-column prop="name" align="center" label="权限名称"></el-table-column>
      <el-table-column prop="sort" align="center" label="权限序号"></el-table-column>
      <el-table-column prop="code" align="center" label="权限code"></el-table-column>
      <el-table-column prop="icon" align="center" label="权限图标"></el-table-column>
      <el-table-column prop="url" align="center" label="权限url"></el-table-column>
      <!-- <el-table-column prop="status" align="center" label="权限状态"></el-table-column> -->
      <el-table-column align="center" label="权限状态">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
       <el-table-column fixed="right" label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          >编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <span class="pagein">
      <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 30, 50, 100]"
        :page-size="10"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </span>

    <el-dialog
      v-if="centerDialogVisible"
      :title="disval==1?'创建权限':'编辑权限'"
      :visible.sync="centerDialogVisible"
      :modal-append-to-body="false"
      width="50%"
      center
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <!-- <el-form-item label="权限id" prop="id">
          <el-input
            v-model="ruleForm.id"
            maxlength="255"
            clearable
            :disabled="disval==1?false:true"
          ></el-input>
        </el-form-item> -->
        <el-form-item label="权限名称" prop="name">
          <el-input
            v-model="ruleForm.name"
            maxlength="255"
            clearable
            :disabled="disval==1?false:true"
          ></el-input>
        </el-form-item>
        <el-form-item label="权限icon" prop="icon">
          <el-input v-model="ruleForm.icon" maxlength="255" clearable></el-input>
        </el-form-item>
        <el-form-item label="权限sort" prop="sort">
          <el-input v-model="ruleForm.sort" maxlength="255" clearable></el-input>
        </el-form-item>
        <el-form-item label="权限code" prop="code">
          <el-input v-model="ruleForm.code" maxlength="255" clearable  :disabled="disval==1?false:true"></el-input>
        </el-form-item>
        <el-form-item label="权限url">
          <el-input v-model="ruleForm.url" maxlength="255" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确认保存</el-button>
          <el-button @click="resetForm('ruleForm')">表单重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "operation_permission",
  data() {
    return {
      permissionlist: [],
      disval: 1,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      loading:false,
      centerDialogVisible: false,
      ruleForm: {
        id: "",
        icon: "",
        name: "",
        sort: "",
        code:"",
        url: ""
      },
      rules: {
        name: [{ required: true, message: "请输入权限名称", trigger: "blur" }],
        code: [{ required: true, message: "请输入权限code", trigger: "blur" }],
        sort: [{ required: true, message: "请输入权限sort", trigger: "blur" }],
        url: [{ required: true, message: "请输入权限url", trigger: "blur" }],
        icon: [{ required: true, message: "请输入权限icon", trigger: "blur" }]
      }
    };
  },
  methods: {
    //获取列表数据
    getlist() {
      let _Url =
        "operatorPermission?pageNum=" +
        this.pageNum +
        "&pageSize=" +
        this.pageSize;
      this.loading = true;
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.total = res.data.total;
        if (res.data.total < 1) {
          this.$message("没有查询到相关数据信息");
          this.permissionlist = [];
        } else {
          let _data = [...res.data.data];
          _data.sort(this.Until.compare("score",2));
          for (let i in _data) {
            if (_data[i].status == 1) {
              _data[i].status = true;
            } else if (_data[i].status == 2) {
              _data[i].status = false;
            }
          }
          this.permissionlist = _data;
        }
      });
    },
    //表格翻页
    handleCurrentChange(val) {
      this.pageNum = val;
      this.getlist();
    },
    //修改每页请求数据条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getlist();
    },
    //显示弹框
    openDialog(val) {
      if (val == 1) {
        this.actuserobj = {};
        for (let key in this.ruleForm) {
          this.ruleForm[key] = "";
        }
      }
      this.disval = val ? val : "";
      this.centerDialogVisible = true;
    },
    //保存
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.savedata();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //重置
    resetForm(formName) {
      if (this.actuserobj && this.actuserobj.id) {
        this.getidinfo(this.actuserobj.id);
      } else {
        this.$refs[formName].resetFields();
      }
    },
    //提交保存数据
    savedata(){
      let _Url = "operatorPermission",
        _parms = {};
      for (let key in this.ruleForm) {
        if (this.ruleForm[key]) {
          _parms[key] = this.ruleForm[key];
        }
      }
      if (this.disval == 1) {
        this.$http.post(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getlist();
          }
        });
      } else if (this.disval == 2) {
        this.$http.put(_Url, _parms).then(res => {
          if (res.status == 200) {
            this.$message.success("保存成功");
            this.centerDialogVisible = !this.centerDialogVisible;
            this.getlist();
          }
        });
      }
    },
    //修改用户状态
    changestatus(obj) {
      let _Url = "operatorPermission/status",
        _parms = {},
        _status = "";
      _status = obj.status ? 1 : 2;
      _parms = {
        status: _status,
        id: obj.id
      };
      this.$http.put(_Url, _parms).then(res => {
        if (res.status == 200) {
          this.$message.success("修改成功");
          this.getlist();
        }
      });
    },
    //点击编辑某条数据
    handleedit(obj) {
      this.actuserobj = obj;
      this.getidinfo(obj.id);
    },
    //通过ID查询某个数据信息
    getidinfo(id) {
      this.$http.get("operatorPermission/" + id).then(res => {
        if (res.status == 200) {
          this.openDialog(2);
          let obj = { ...res.data };
          obj.remark = obj.remark ? obj.remark : "";
          for (let key in obj) {
            this.ruleForm[key] = obj[key];
          }
        }
      });
    },
  },
  created() {
    this.getlist();
  }
};
</script>

<style>
</style>


